<template>
  <!-- 医院管理 -->
  <div class="indexPage">
    <a-breadcrumb style="margin: 16px 0;padding-left:30px;" separator=">">
      <a-breadcrumb-item>
        <a-icon type="home" />首页
      </a-breadcrumb-item>
      <a-breadcrumb-item>医院管理</a-breadcrumb-item>
    </a-breadcrumb>
    <a-layout-content
      :style="{ background: '#fff', padding: '24px', margin: 0, minHeight: '280px',overflow:'scroll',height:'calc(100vh - 110px)'}"
    >
      <div class="control">
        <div class="searchGroup">
          <el-date-picker
            v-model="filterDate"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy-MM-dd"
            :editable="false"
            class="picker"
            unlink-panels
            style="margin-right:10px;"
          ></el-date-picker>
          <!-- <el-select v-model="hosLevel" placeholder="按医院级别查询" style="width:148px;margin:0 10px;">
            <el-option label="级别一" value="shanghai"></el-option>
            <el-option label="级别二" value="beijing"></el-option>
          </el-select> -->
          <el-input placeholder="按医院名称模糊查询" v-model="searchText" clearable class="searchInput"></el-input>
          <el-button class="searchBtn" @click="handleSearch">查询</el-button>
        </div>
        <el-button class="addBtn" @click="addHosDialog = true">添加医院</el-button>
      </div>
      <el-table
        :data="tableData"
        style="width: 100%"
        border
        :header-cell-style="{background:'#F2F2F2'}"
        v-loading="loading"
      >
        <el-table-column prop="id" label="编号" width="80" align="center"></el-table-column>
        <el-table-column prop="name" label="医院名称" width="200" align="center"></el-table-column>
        <el-table-column prop="upHospital" label="上级医院" width="200" align="center"></el-table-column>
        <el-table-column prop="address" label="地址" width="310" align="center"></el-table-column>
        <el-table-column prop="principalName" label="负责人" width="100" align="center"></el-table-column>
        <el-table-column label="操作" width="150" align="center">
          <template slot-scope="scope" class="btns">
            <el-button icon="el-icon-edit" @click.stop="edit(scope.row)" size="mini">修改</el-button>
            <el-button icon="el-icon-delete" size="mini" @click.stop="delRow(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
         background
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="pagesize"
        layout="prev, pager, next, jumper"
        :total="total"
        class="pages"
      ></el-pagination>
      <!-- 添加医院弹框 -->
      <el-dialog
        title="添加医院"
        :visible.sync="addHosDialog"
        class="addPlatform"
        width="760px"
        :close-on-click-modal="false"
      >
        <el-form :model="addHosform" ref="addHosform">
          <el-form-item
            label="医院名称"
            :label-width="formLabelWidth"
            prop="name"
            :rules="[{ required: true, message: '请输入医院名称', trigger: 'blur' }]"
          >
            <el-input v-model="addHosform.name" autocomplete="off" placeholder="请输入医院名称"></el-input>
          </el-form-item>
          <el-form-item
            label="上级医院"
            :label-width="formLabelWidth"
            
          >
          <!-- prop="upHospital"
            :rules="{ required: true, message: '请选择上级医院', trigger: 'change' }" -->
            <el-select v-model="addHosform.upHospital" placeholder="请选择上级医院">
              <el-option v-for="(item, index) in hosList" :key="index" :label="item.name" :value="item.name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="省市区" :label-width="formLabelWidth"
            prop="selectedOptions"
            :rules="[{ type:'array',required: true, message: '请选择省市区', trigger: 'change' }]"
          >
            <el-cascader size="large" :options="options" v-model="addHosform.selectedOptions" placeholder="请选择医院地址"></el-cascader>
          </el-form-item>
          <el-form-item label="详细地址" :label-width="formLabelWidth">
            <el-input
              type="textarea"
              v-model="addHosform.detailAddress"
              autocomplete="off"
              placeholder="请输入详细地址"
            ></el-input>
          </el-form-item>
          <el-form-item
            label="负责人"
            :label-width="formLabelWidth"
          >
            <el-select v-model="addHosform.principal" placeholder="请选择负责人">
              <el-option v-for="(item, index) in docList" :key="index" :label="item.name" :value="item.id"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="submitCreate('addHosform')">确 定</el-button>
        </div>
      </el-dialog>

      <!-- 编辑信息 -->
      <el-dialog
        title="修改项目"
        :visible.sync="editHosDialog"
        class="addPlatform"
        width="760px"
        :close-on-click-modal="false"
      >
        <el-form :model="editHosform" ref="editHosform">
          <el-form-item
            label="医院名称"
            :label-width="formLabelWidth"
            prop="name"
            :rules="[{ required: true, message: '请输入医院名称', trigger: 'blur' }]"
          >
            <el-input v-model="editHosform.name" autocomplete="off" placeholder="请输入医院名称"></el-input>
          </el-form-item>
          <el-form-item
            label="上级医院"
            :label-width="formLabelWidth"
            
          >
          <!-- prop="upHospital"
            :rules="{ required: true, message: '请选择上级医院', trigger: 'change' }" -->
            <el-select v-model="editHosform.upHospital" placeholder="请选择上级医院">
              <el-option v-for="(item, index) in hosList" :key="index" :label="item.name" :value="item.name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="省市区" :label-width="formLabelWidth"
            prop="editOptions"
            :rules="[{ type:'array',required: true, message: '请选择省市区', trigger: 'change' }]"
          >
            <el-cascader size="large" :options="options" v-model="editHosform.editOptions" placeholder="请选择医院地址"></el-cascader>
          </el-form-item>
          <el-form-item label="详细地址" :label-width="formLabelWidth">
            <el-input
              type="textarea"
              v-model="editHosform.detailAddress"
              autocomplete="off"
              placeholder="请输入详细地址"
            ></el-input>
          </el-form-item>
          <el-form-item
            label="负责人"
            :label-width="formLabelWidth"
          >
            <el-select v-model="editHosform.principal" placeholder="请选择负责人">
              <el-option v-for="(item, index) in docList" :key="index" :label="item.name" :value="item.id"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="submitModify('editHosform')">确 定</el-button>
        </div>
      </el-dialog>
    </a-layout-content>
  </div>
</template>
<script>
import { regionData,CodeToText,TextToCode } from "element-china-area-data";
import {mixin} from '../mixins/allDocAndPat'
import { createHos ,getHosList,updateHos} from '../service/getData'
export default {
  mixins: [mixin],
  data() {
    return {
      loading:false,
      searchText: "", // 输入的搜索内容
      filterDate: "", // 选择的开始结束日期
      addHosDialog: false, // 是否显示添加医院的弹框
      editHosDialog: false, // 是否显示编辑医院的弹框
      currentPage: 1, // 当前页
      pagesize: 15,
      total: 0, // 总条数
      options: regionData,// 省市区数据
      addHosform: {
        // 添加医院的表单填写项
        name:'',// 医院名称
        detailAddress:'',// 医院详细地址
        upHospital :'',// 上级医院
        principal:'',// 负责人 id
        selectedOptions:[],// 选择的省市区代码
        // code:'000000'
      },
      editHosform: {
        // 编辑的item的数据
      },

      formLabelWidth: "80px",
      tableData: [
        // {
        //   id: "1",
        //   name: "医院名称",
        //   upHospital: "上级医院",
        //   province: "本项目主要研究首都医科大学附属医院",
        //   city: "2019-09-09",
        //   county: "4",
        //   detailAddress: "4",
        //   address:'拼接后的完整地址'
        //   principal:'1',// 负责人id
        //   principalName:'',// 负责人名字
        // }
      ]
    };
  },
  methods: {
    // 创建医院
    submitCreate(ref) {
      this.$refs[ref].validate(valid => {
        if (valid) {
          let addressObj = this.addressCode2Text(this.addHosform.selectedOptions);
          // console.log(addObj);
          const params = {
            ...this.addHosform,
            ...addressObj
          }
          delete params.selectedOptions;
          this.hendleCreate(params);
        } else {
          return false;
        }
      });
    },
    // 提交修改
    submitModify(ref) {
      this.$refs[ref].validate(valid => {
        if (valid) {
          let addressObj = this.addressCode2Text(this.editHosform.editOptions);
          let { id , principal , detailAddress , upHospital,name} = this.editHosform;
          const newData = {
            id,principal,detailAddress,upHospital,name,
            ...addressObj
          }
          this.handleUpdate(newData);
          
        } else {
          return false;
        }
      });
    },
    // 点击修改
    edit(row) {
      // console.log(row);
      let { province , city , county} = row;
      let addressTextArr = this.addressText2Code([province,city,county]);
      row.editOptions = addressTextArr
      this.editHosform = row;
      this.editHosDialog = true;
    },
    // 删除
    delRow(row) {
      // console.log(row);
    },
    // 创建医院
    async hendleCreate(params){
      let res = await createHos(params);
      let {
        code,
        data,
        message
      } = res.data;
      if(code === 200){
         this.$message({
          message: "添加成功",
          type: "success"
        });
        this.addHosDialog = false;
        const params = {
          size: this.pagesize,
          page: 1,
          queryElement: {
            flag: 0,
            queryItems: []
          }
        };
        this.handleGetList(params);
        this.addHosform =  {
          name:'',// 医院名称
          detailAddress:'',// 医院详细地址
          upHospital :'',// 上级医院
          principal:'',// 负责人 id
          selectedOptions : [],
        }
      } else {
        this.$message.error(message);
      }

    },
    // 获取医院列表
    async handleGetList(params) {
      this.loading = true;
      let res = await getHosList(params);
      this.loading = false;
      let {
        code,
        data,
        data: { total, resultList },
        message
      } = res.data;
      if (code === 200) {
        const _this = this;
        resultList.forEach(item => {
          item.address = `${item.province}${item.city}${item.county}${item.detailAddress}`
          _this.docList.some(doc => {
            if(doc.id == item.principal){
              item.principalName = doc.name;
              return true
            }
          })
        });
        this.total = total;
        this.tableData = resultList;
      } else {
        this.$message.error(message);
      }
    },
    // 修改医院
    async handleUpdate(params) {
      let res = await updateHos(params);
      let { code, message } = res.data;
      if (code === 200) {
        this.$message({
          message: "修改成功",
          type: "success"
        });
        let { id, province,city,county,detailAddress,upHospital, principal,name} = params;
        let address = `${province}${city}${county}${detailAddress}`
        let principalName;
        this.docList.some(doc => {
          if(doc.id == principal){
            principalName = doc.name;
            return true;
          }
        })
        this.tableData.some(item => {
          if (item.id == id) {
            item.province = province;
            item.city = city;
            item.county = county;
            item.detailAddress = detailAddress;
            item.upHospital = upHospital;
            item.principal = principal;
            item.principalName = principalName;
            item.address = address;
            return true;
          }
        });
        this.editHosDialog = false;
        this.editHosform = {};
      } else {
        this.$message.error(message);
      }
    },
    handleCurrentChange(val) {
      const params = {
        size: this.pagesize,
        page: val,
        queryElement: {
          flag: 0,
          queryItems: []
        }
      };
      let queryItem = {};
      let queryItem2 = {};
      if (this.searchText.trim() != "") {
        queryItem = {
          columnName: "name",
          value: this.searchText,
          method: "like"
        };
        params.queryElement.queryItems.push(queryItem);
      }
      if(this.filterDate.length != 0){
        queryItem2 = {
          columnName: "createTime",
          value:{
            "start": this.filterDate[0],
            "end": this.filterDate[1],
          },
          method: "between"
        };
        params.queryElement.queryItems.push(queryItem2);
      }
    this.handleGetList(params);
    },
    // 搜索医院
    handleSearch() {
      const params = {
        size: this.pagesize,
        page: 1,
        queryElement: {
          flag: 0,
          queryItems: []
        }
      };
      let queryItem = {};
      let queryItem2 = {};
      if (this.searchText.trim() != "") {
        queryItem = {
          columnName: "name",
          value: this.searchText,
          method: "like"
        };
        params.queryElement.queryItems.push(queryItem);
      }
      if(this.filterDate.length != 0){
        queryItem2 = {
          columnName: "createTime",
          value:{
            "start": this.filterDate[0],
            "end": this.filterDate[1],
          },
          method: "between"
        };
        params.queryElement.queryItems.push(queryItem2);
      }
      this.handleGetList(params);
    },
    addressCode2Text(targetArr){
      let [province,city,county] = targetArr;
          province = CodeToText[province]
          city = CodeToText[city]
          county = CodeToText[county]
          return {
            province,
            city,
            county
          }
    },
    addressText2Code(targetArr){
      let [province_text,city_text,county_text] = targetArr;
          let province = TextToCode[province_text].code;
          let city = TextToCode[province_text][city_text].code;
          let county= TextToCode[province_text][city_text][county_text].code;
          return [
            province,
            city,
            county
          ]
    }
  },
  created() {
    const params = {
      size: this.pagesize,
      page: 1,
      queryElement: {
        flag: 0,
        queryItems: []
      }
    };
    this.handleGetList(params);
  }
};
</script>
