<template>
  <!-- 问卷计划下的 =====> 每日计划 -->
  <div>
    <div class="dailyPlan">
      <a-alert description="请先设置所有每日计划后，再进行制定推送时间。" type="success" closable showIcon />
      <div class="control">
        <div style="font-weight:bold;font-size:16px;">每日计划</div>
        <div>
          <el-button class="addBtn" @click="handleWorkOutPush" :disabled='useStatus == 1'>制定全局推送时间</el-button>
          <el-button class="addBtn" @click="addDailyplanDialog = true" :disabled='useStatus == 1'>添加每日计划</el-button>
        </div>
      </div>
      <div class="table" style="margin-top:20px;">
        <!-- width:975px; -->
        <el-table
          :data="tableData"
          style="width: 100%"
          border
          :header-cell-style="{background:'#F2F2F2',fontWeight:'normal'}"
        >
          <el-table-column prop="id" label="ID" width="50" align="center"></el-table-column>
          <el-table-column prop="type" label="类型" width="80" align="center">
            <template slot-scope="scope">
              {{scope.row.type == 1 ? '常规问卷' : '特殊问卷'}}
            </template>
          </el-table-column>
          <el-table-column prop="day" label="天数" width="80" align="center">
            <template slot-scope="scope">
              {{'第' + scope.row.day + '天'}}
            </template>
          </el-table-column>
          <el-table-column prop="stratTime" label="问卷生效时间" width="120" align="center"></el-table-column>
          <el-table-column prop="endTime" label="问卷失效时间" width="120" align="center"></el-table-column>
          <el-table-column prop="tagging" label="标注" width="120" align="center"></el-table-column>
          <el-table-column prop="completionText" label="完成提示文字" width="110" align="center"></el-table-column>
          <el-table-column prop="pushTime" label="推送时间" width="100" align="center">
            <template slot-scope="scope">
              {{scope.row.pushTime ? scope.row.pushTime : globalPushTime}}
            </template>
          </el-table-column>
          <el-table-column label="操作" width="260" align="center">
            <template slot-scope="scope" class="btns">
              <el-button @click.stop="customPush(scope.row)" size="mini" :disabled='useStatus == 1'>定制推送</el-button>
              <el-button icon="el-icon-edit" @click.stop="edit(scope.row)" size="mini" :disabled='useStatus == 1'>修改</el-button>
              <el-button icon="el-icon-delete" size="mini">删除</el-button>
              <a-icon
                type="right-circle"
                @click="toTask(scope.row)"
                style="font-size:20px;color:#72B4DE;margin-left: 10px;vertical-align: middle;cursor:pointer;"
              />
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!-- 添加每日计划的dialog -->
      <el-dialog
        title="添加每日计划"
        :visible.sync="addDailyplanDialog"
        class="addPlatform aduitDoc"
        width="760px"
        :close-on-click-modal="false"
        destroy-on-close
      >
        <el-form :model="createDeilyForm" ref="createDeilyForm">
          <el-form-item
            label="问卷类型"
            :label-width="formLabelWidth"
            prop="type"
            :rules="[{ required: true, message: '请选择问卷类型',trigger: 'change' }]"
          >
            <el-radio-group v-model="createDeilyForm.type">
              <el-radio label="1">常规问卷</el-radio>
              <el-radio label="0">特殊问卷</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item
            label="天数"
            :label-width="formLabelWidth"
            prop="day"
            :rules="{ required: true, message: '请选择天数', trigger: 'change' }"
          >
            <el-select v-model="createDeilyForm.day" placeholder="请选择天数">
              <el-option  label="第0天" :value="0"></el-option>
              <el-option  v-for="(item, index) in days" :key="index" :label="'第' + item + '天'" :value="item"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item
            label="问卷生效时间"
            :label-width="formLabelWidth"
            prop="stratTime"
            :rules="{type: 'string', required: true, message: '请选择问卷生效时间', trigger: 'change' }"
          >
            <el-time-picker
              style="width: 100%;"
              v-model="createDeilyForm.stratTime"
               value-format="HH:mm"
               format='HH:mm'
                :editable='false'
              placeholder="选择问卷生效时间">
            </el-time-picker>
          </el-form-item>
          <el-form-item
            label="问卷失效时间"
            :label-width="formLabelWidth"
            prop="endTime"
            :rules="{type: 'string', required: true, message: '请选择问卷失效时间', trigger: 'change' }"
          >
            <el-time-picker
              style="width: 100%;"
              value-format="HH:mm"
               format='HH:mm'
              :editable='false'
              v-model="createDeilyForm.endTime"
              placeholder="选择问卷失效时间">
            </el-time-picker>
          </el-form-item>
          <el-form-item
            label="标注"
            :label-width="formLabelWidth"
          >
            <el-input
              type="textarea"
              v-model="createDeilyForm.tagging"
              autocomplete="off"
              placeholder="请输入标注(即为问卷列表上的标注文字)"
            ></el-input>
          </el-form-item>
          <el-form-item
            label="完成提示文字"
            :label-width="formLabelWidth"
          >
            <el-input
              type="textarea"
              v-model="createDeilyForm.completionText"
              autocomplete="off"
              placeholder="请输入完成提示文字(即用户完成今日所有任务后的提示文字)"
            ></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="submitNewPlan('createDeilyForm')">确 定</el-button>
        </div>
      </el-dialog>

      <!-- 修改每日计划表格的dialog -->
      <el-dialog
        title="修改每日计划"
        :visible.sync="editDailyplanDialog"
        class="addPlatform aduitDoc"
        width="760px"
        :close-on-click-modal="false"
        destroy-on-close
      >
        <el-form :model="editPlanTable" ref="editPlanTable">
          <el-form-item
            label="问卷类型"
            :label-width="formLabelWidth"
            prop="type"
            :rules="[{ required: true, message: '请选择问卷类型',trigger: 'change' }]"
          >
            <el-radio-group v-model="editPlanTable.type">
              <el-radio :label="1">常规问卷</el-radio>
              <el-radio :label="0">特殊问卷</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item
            label="天数"
            :label-width="formLabelWidth"
            prop="day"
            :rules="{ required: true, message: '请选择天数', trigger: 'change' }"
          >
            <el-select v-model="editPlanTable.day" placeholder="请选择天数">
              <el-option  label="第0天" :value="0"></el-option>
              <el-option  v-for="(item, index) in days" :key="index" :label="'第' + item + '天'" :value="item"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item
            label="问卷生效时间"
            :label-width="formLabelWidth"
            prop="stratTime"
            :rules="{type: 'string', required: true, message: '请选择问卷生效时间', trigger: 'change' }"
          >
            <el-time-picker
              style="width: 100%;"
              value-format="HH:mm"
              format='HH:mm'
              :editable='false'
              v-model="editPlanTable.stratTime"
              placeholder="选择问卷生效时间">
            </el-time-picker>
          </el-form-item>
          <el-form-item
            label="问卷失效时间"
            :label-width="formLabelWidth"
            prop="endTime"
            :rules="{type: 'string', required: true, message: '请选择问卷失效时间', trigger: 'change' }"
          >
            <el-time-picker
              style="width: 100%;"
              value-format="HH:mm"
              format='HH:mm'
              :editable='false'
              v-model="editPlanTable.endTime"
              placeholder="选择问卷失效时间">
            </el-time-picker>
          </el-form-item>
          <el-form-item
            label="标注"
            :label-width="formLabelWidth"
          >
            <el-input
              type="textarea"
              v-model="editPlanTable.tagging"
              autocomplete="off"
              placeholder="请输入标注(即为问卷列表上的标注文字)"
            ></el-input>
          </el-form-item>
          <el-form-item
            label="完成提示文字"
            :label-width="formLabelWidth"
          >
            <el-input
              type="textarea"
              v-model="editPlanTable.completionText"
              autocomplete="off"
              placeholder="请输入完成提示文字(即用户完成今日所有任务后的提示文字)"
            ></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="submitPlanTable('editPlanTable')">确 定</el-button>
        </div>
      </el-dialog>


    <!-- 制定全局推送时间 -->
    <el-dialog
      title="制定全局推送计划"
      :visible.sync="addGlobalPushTimeDialog"
      class="addPlatform aduitDoc"
      width="760px"
      top='25vh'
      :close-on-click-modal="false"
      destroy-on-close
    >
          <el-form ref="addGlobal" :model="addGlobal">
            <el-form-item
              label="提醒时间"
              :label-width="formLabelWidth"
              v-for="(item, index) in addGlobal.timeArr" :key="index"
            >
              <el-col :span="6">
                  <div style="color:#333;font-size:12px;border:1px solid #DCDFE6;height:31px;line-height:32px;padding:8px 10px;border-radius:4px;display:inline;">每天第{{index + 1}}次</div>
            </el-col>
            <el-col :span="12">
                <el-form-item>
                    <el-time-picker
                      style="width: 100%;"
                      value-format="HH:mm"
                      format='HH:mm'
                      :editable='false'
                      v-model="item.time"
                      placeholder="选择问卷生效时间">
                    </el-time-picker>
                </el-form-item>
            </el-col>
            <el-col :span="3">
                <i class="el-icon-circle-plus-outline" @click="increaGlobalTime"></i>
                <i class="el-icon-remove-outline" @click="decreGlobalTime(index)"></i>
            </el-col>
            </el-form-item>
          </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="submitNewGlobalPush('addGlobal')">确 定</el-button>
      </div>
    </el-dialog>


    <!-- 个性定制推送时间 -->
    <el-dialog
      title="定制推送计划"
      :visible.sync="addCustomPushTimeDialog"
      class="addPlatform aduitDoc"
      width="760px"
      top='25vh'
      :close-on-click-modal="false"
      destroy-on-close
    >
          <el-form :model="addCustom" ref="addCustom">
            <el-form-item
              label="提醒时间"
              :label-width="formLabelWidth"
              v-for="(item, index) in addCustom.timeArr" :key="index"
              
            >
              <el-col :span="6">
                  <div style="color:#333;font-size:12px;border:1px solid #DCDFE6;height:31px;line-height:32px;padding:8px 10px;border-radius:4px;display:inline;">每天第{{index + 1}}次</div>
            </el-col>
            <el-col :span="12">
                <el-form-item>
                    <el-time-picker
                      style="width: 100%;"
                      v-model="item.time"
                      value-format="HH:mm"
                      format='HH:mm'
                      :editable='false'
                      placeholder="选择问卷生效时间">
                    </el-time-picker>
                </el-form-item>
            </el-col>
            <el-col :span="3">
                <i class="el-icon-circle-plus-outline" @click="increaCustomTime"></i>
                <i class="el-icon-remove-outline" @click="decreCustomTime(index)"></i>
            </el-col>
            </el-form-item>
          </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="submitNewCustomPush('addCustom')">确 定</el-button>
      </div>
    </el-dialog>
    </div>
  </div>
</template>
<script>
import { getDailyPlanList ,createDailyPlan,updateDailyPlan,updateSurveyPlan,findSurveyPlan } from '../service/getData'
import { parseTime2String } from '../tools/utils'
export default {
  data() {
    return {
      globalPushTime:'',// 全局推送时间
      useStatus: '',//问卷启用状态
      tableData: [],
      formLabelWidth: "120px",
      addPlanType: "1", // 添加推送计划的类型 1.全局提醒  2.定制提醒
      addDailyplanDialog: false, // 添加每日计划弹框
      editDailyplanDialog: false, // 修改每日计划弹框
      addGlobalPushTimeDialog:false,// 制定全局推送时间弹框
      addCustomPushTimeDialog:false,// 个性定制推送时间弹框
      days:'',// 问卷持续天数，供下拉框选择
      questionnairePlanId : '',// 问卷计划id
      createDeilyForm: {
        // 添加每日计划
      },
      editPlanTable: {
        // 修改每日计划表格内容
      },
      addGlobal:{
        //   添加全局推送
        timeArr : [{time:''}]
      },
      surveyItemId : '',// 问卷计划项id
      addCustom:{
        // 定制推送
        timeArr : [{time:''}]
      }
    };
  },
  methods: {
    // 添加每日计划
    submitNewPlan(ref) {
      this.$refs[ref].validate(valid => {
        if (valid) {
          const params = {
            "questionnairePlanId": this.questionnairePlanId,
            "questionnairePlanItemDtos": [this.createDeilyForm]
          }
          this.handleCreateDailyPlan(params);
        } else {
          return false;
        }
      });
    },
    // 提交修改每日计划的表单
    submitPlanTable(ref) {
      this.$refs[ref].validate(valid => {
        if (valid) {
          let { id,day,stratTime,endTime,tagging,completionText,type} = this.editPlanTable;
          const params = {id,day,stratTime,endTime,tagging,completionText,type}
          this.handleUpdateDailyPlan(params);
        } else {
          return false;
        }
      });
    },
    // 提交全局推送
    submitNewGlobalPush(ref){
      this.$refs[ref].validate(valid => {
        if (valid) {
          if(this.addGlobal.timeArr[0].time == '' || !this.addGlobal.timeArr[0].time){
              this.$message({
              message: '您未选择任何时间',
              type: 'warning'
            });
            return;
          }
          this.addGlobal.timeArr.forEach((item,index) => {
            if(!item.time){
              this.addGlobal.timeArr.splice(index,1)
            }
          });
          let pushTime = this.addGlobal.timeArr.reduce( (pre,current) => {
            return pre + ',' + current.time
          },'')
          const params = {
            id : this.questionnairePlanId,
            pushTime : pushTime.substring(1)
          }
          this.handleGlobalTime(params);
        } else {
          return false;
        }
      });
    },
    // 提交定制提醒
    submitNewCustomPush(ref){
      this.$refs[ref].validate(valid => {
        if (valid) {
          if(this.addCustom.timeArr[0].time == '' || !this.addCustom.timeArr[0].time){
              this.$message({
              message: '您未选择任何时间',
              type: 'warning'
            });
            return;
          }
          this.addCustom.timeArr.forEach((item,index) => {
            if(!item.time){
              this.addCustom.timeArr.splice(index,1)
            }
          });
          let pushTime = this.addCustom.timeArr.reduce( (pre,current) => {
            return pre + ',' + current.time
          },'')
          const params = {
            id : this.surveyItemId,
            pushTime : pushTime.substring(1)
          }
          this.handleCustomTime(params);
        } else {
          return false;
        }
      });

    },
    // 医生信息修改
    edit(row) {
      // console.log(row);
      this.editPlanTable = _.cloneDeep(row);
      this.editDailyplanDialog = true;
    },
    // 创建每日计划
    async handleCreateDailyPlan(params){
      let res = await createDailyPlan(params);
      let {
        code,
        message
      } = res.data;
      if (code === 200) {
        this.handleGetList({questionnairePlanId : this.questionnairePlanId});
        this.addDailyplanDialog = false;
        this.createDeilyForm = {};
      } else {
        this.$message.error(message);
      }
    },
    // 修改每日计划
    async handleUpdateDailyPlan(params){
      let res = await updateDailyPlan(params);
      let {
        code,
        message
      } = res.data;
      if (code === 200) {
        this.handleGetList({questionnairePlanId : this.questionnairePlanId});
        this.editDailyplanDialog = false;
        this.editPlanTable = {};
      } else {
        this.$message.error(message);
      }
    },
    // 获取每日计划列表
    async handleGetList(query) {
      let res = await getDailyPlanList(query);
      let {
        code,
        data,
        message
      } = res.data;
      if (code === 200) {

        this.tableData = data;

      } else {
        this.$message.error(message);
      }
    },
    // 制定推送时间弹框
    handleWorkOutPush(row){
        this.$confirm('是否已设置完所有每日计划，并且每日计划中的问卷都已设置完成？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.addGlobalPushTimeDialog = true;
          this.handleFindSurveyPlan({id : this.questionnairePlanId});
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '您已取消'
          });          
        });
    },
    // 个性定制推送时间
    customPush(row){
      if(row.pushTime){
        let pushTimeArr = row.pushTime.split(',');
         let resArr = [];
         pushTimeArr.forEach(element => {
           let obj = {time : element}
           resArr.push(obj)
         });
        this.addCustom.timeArr = resArr;
      } else {
        this.addCustom.timeArr = [{time:''}];
      }
      this.surveyItemId = row.id;
      this.addCustomPushTimeDialog = true;
    },
    // 全局推送时间增加
    increaGlobalTime(){
      let flag = this.addGlobal.timeArr.some( item => {
        if(item.time == '' || !item.time){
          this.$message({
            message: '您还有未填写项',
            type: 'warning'
        });
        return true;
        }
        return false;
      })
      if(flag){
        return;
      }
      this.addGlobal.timeArr.push({time:''})
    },
    decreGlobalTime(index){
      if(this.addGlobal.timeArr.length == 1){
        this.$message({
          message: '至少保留一项',
          type: 'warning'
        });
        return;
      }
      this.addGlobal.timeArr.splice(index,1);
    },
    // 提交全局推送计划
    async handleGlobalTime(params){
      let res = await updateSurveyPlan(params);
      let {code,message} = res.data;
      if(code === 200){
         this.$message({
          message: "制定成功",
          type: "success"
        });
        this.addGlobalPushTimeDialog = false;
        this.handleFindSurveyPlan({id : this.questionnairePlanId});
      } else {
        this.$message.error(message);
      }
    },
    // 根据问卷计划查找推送时间,用于处理全局推送时间
    async handleFindSurveyPlan(query){
      let res = await findSurveyPlan(query);
      let {code ,message,data} = res.data;
      if(code === 200){
         let { pushTime : pushTimeStr } = data;
         if(pushTimeStr){
            let pushTimeArr = pushTimeStr.split(',');
            let resArr = [];
            pushTimeArr.forEach(element => {
              let obj = {time : element}
              resArr.push(obj)
            });
            this.addGlobal.timeArr = resArr;
         }
         
      } else {
        this.$message.error(message);
      }
    },

    // 个性定制推送时间项增加
    increaCustomTime(){
      let flag = this.addCustom.timeArr.some( item => {
        if(item.time == '' || !item.time){
          this.$message({
            message: '您还有未填写项',
            type: 'warning'
        });
        return true;
        }
        return false;
      })
      if(flag){
        return;
      }
      this.addCustom.timeArr.push({time:''})
    },
    decreCustomTime(index){
      if(this.addCustom.timeArr.length == 1){
        this.$message({
          message: '至少保留一项',
          type: 'warning'
        });
        return;
      }
      this.addCustom.timeArr.splice(index,1);

    },
    // 提交个性定制推送计划
    async handleCustomTime(params){
      let res = await updateDailyPlan(params);
      let {code,message} = res.data;
      if(code === 200){
         this.$message({
          message: "制定成功",
          type: "success"
        });
        this.addCustomPushTimeDialog = false;
        this.addCustom.timeArr = [{time:''}];
        this.handleGetList({questionnairePlanId : this.questionnairePlanId});
      } else {
        this.$message.error(message);
      }
    },
    // 去往下一级:任务
    toTask(row) {
      // console.log(row)
      this.$router.push({path:"/home/projectDetail/task",query:{questionnairePlanItemId : row.id,currentDay : row.day,duration : this.days,useStatus:this.useStatus}});// 问卷计划项id，当前天数,问卷持续天数
    }
  },
  filters:{
    parseTime(timestamp){
     return parseTime2String(timestamp)
    },
    showStatus(status){
      switch(status){
        case 0 :{
          return '进行中'
          break
        }
      }

    },
  },
  created() {
    let {questionnairePlanId,days,pushtime,useStatus} = this.$route.query;
    this.globalPushTime = pushtime;
    this.useStatus = useStatus;
    this.questionnairePlanId = questionnairePlanId;
    this.days = parseInt(days);
    this.handleGetList({questionnairePlanId : questionnairePlanId});
    this.handleFindSurveyPlan({id : questionnairePlanId});

  },
};
</script>
<style lang="less" scoped>
.ant-alert-success {
  background-color: #def0d8;
  border: 1px solid #def0d8;
  font-weight: bold;
  color: #81ac69;
}
.control {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
  margin-top: 20px;
  .addBtn {
    background: #468cc8;
    color: #fff;
    border: none;
    height: 32px;
    padding: 8px 20px;
    font-weight: normal;
  }
  .addBtn:first-child {
    background: #fff;
    color: #468cc8;
    border: 1px solid #468cc8;
    height: 32px;
    padding: 8px 20px;
