import Vue from 'vue'
import VueRouter from 'vue-router'
import Notfount from '../views/404'
import Home from '../views/Home.vue'
import Login from '../views/Login.vue'
import Index from '../views/Index.vue'
import Platform from '../views/Platform.vue'
import Project from '../views/Project.vue'
import Detail from '../views/Detail.vue'
import surveyPlan from '../views/Plan.vue'
import dailyPlan from '../views/Daily.vue'
import Task from '../views/Task.vue'
import HosList from '../views/HosList.vue'
import DocList from '../views/DocList.vue'
Vue.use(VueRouter)

const routes = [{
        path: '/',
        redirect: '/home',
        meta: { requiresAuth: true }

    },
    {
        path: '/login',
        name: 'login',
        component: Login
    },
    {
        path: '/home',
        // name: 'home',
        component: Home,
        children: [{
                path: '/',
                redirect: 'index',
                meta: { requiresAuth: true }

            },
            {
                path: 'index',
                name: 'index',
                component: Index,
                meta: { requiresAuth: true }

            },
            {
                path: 'platform',
                name: 'platform',
                component: Platform,
                meta: { requiresAuth: true }

            },
            {
                path: 'project',
                name: 'project',
                component: Project,
                meta: { requiresAuth: true }

                // children: [{
                //     path: 'projectDetail',
                //     name: 'projectDetail',
                //     component: Detail
                // }]
            },
            {
                path: 'projectDetail',
                // name: 'projectDetail',
                component: Detail,
                children: [{
                        path: '/',
                        redirect: 'surveyPlan',
                        meta: { requiresAuth: true }

                    },
                    {
                        path: 'surveyPlan', // 问卷计划
                        name: 'surveyPlan',
                        component: surveyPlan,
                        meta: { requiresAuth: true }

                    },
                    {
                        path: 'dailyPlan', // 每日计划
                        name: 'dailyPlan',
                        component: dailyPlan,
                        meta: { requiresAuth: true }

                    },
                    {
                        path: 'task', // 任务
                        name: 'task',
                        component: Task,
                        meta: { requiresAuth: true }

                    }
                ]
            },
            {
                path: 'hosList',
                component: HosList,
                meta: { requiresAuth: true }

            },
            {
                path: 'DocList',
                component: DocList,
                meta: { requiresAuth: true }

            },
            {
                path: '*',
                component: Notfount
            },


        ]
    },
    {
        path: '*',
        component: Notfount
    }
]

const router = new VueRouter({
    routes
})

export default router