<template>
  <!-- 项目详情页选项卡中基本信息模块 -->
  <div>
    <div class="baseinfo">
      <div class="editBtn">
        <el-button type="text" icon="el-icon-edit-outline" @click="editBaseinfoBtn">编辑</el-button>
      </div>
      <el-form label-position="top" label-width="80px" inline>
        <el-form-item label="项目类型:" style="width:450px;margin-right:70px;">
          <el-input v-model="baseinfo.type" readonly></el-input>
        </el-form-item>
        <el-form-item label="项目名称:" style="width:450px;">
          <el-input v-model="baseinfo.name" readonly></el-input>
        </el-form-item>
        <el-form-item label="项目描述:" style="width:975px;margin-right:8px;">
          <el-input type="textarea" v-model="baseinfo.description" readonly></el-input>
        </el-form-item>
        <el-form-item label="研究内容:" style="width:975px;margin-top:8px;">
          <el-input type="textarea" v-model="baseinfo.details" readonly></el-input>
        </el-form-item>
      </el-form>
      <div class="title">
        <span>项目组</span>
        <el-button class="addBtn" @click="createGroupDialog = true;handleGetList({projectId : projectInfo.id});">添加项目组</el-button>
      </div>
      <div class="table" style="margin-top:20px;">
        <!-- width:975px; -->
        <el-table
          :data="tableData"
          style="width: 100%"
          border
          :header-cell-style="{background:'#F2F2F2',fontWeight:'normal'}"
        >
          <el-table-column prop="id" label="ID" width="100" align="center"></el-table-column>
          <el-table-column prop="name" label="项目组名称" width="170" align="center"></el-table-column>
          <el-table-column prop="sampleSize" label="样本量" width="150" align="center"></el-table-column>
          <el-table-column prop="recruitedPatients" label="目前入组人数" width="200" align="center"></el-table-column>
          <el-table-column prop="questionnairePlan.name" label="使用问卷计划" width="200" align="center"></el-table-column>
          <el-table-column label="操作" width="155" align="center">
            <template slot-scope="scope" class="btns">
              <el-button icon="el-icon-edit" @click.stop="edit(scope.row)" size="mini">修改</el-button>
              <el-button icon="el-icon-delete" size="mini" @click.stop="delRow(scope.row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <!-- 修改基本信息 -->
    <el-dialog
      title="修改基本信息"
      :visible.sync="editBaseInfoDialog"
      class="addPlatform"
      width="760px"
      :close-on-click-modal="false"
    >
      <el-form :model="editBaseinfo" ref="editBaseInfo">
        <el-form-item
          label="项目名称"
          :label-width="formLabelWidth"
          prop="name"
          :rules="[{ required: true, message: '请输入项目名称', trigger: 'blur' }]"
        >
          <el-input v-model="editBaseinfo.name" autocomplete="off" placeholder="请输入项目名称"></el-input>
        </el-form-item>
        <el-form-item
          label="项目类型"
          :label-width="formLabelWidth"
          prop="type"
          :rules="{ required: true, message: '请选择项目类型', trigger: 'change' }"
        >
          <el-select v-model="editBaseinfo.type" placeholder="请选择项目类型">
              <el-option label="真实世界研究" value="真实世界研究"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label="项目描述"
          :label-width="formLabelWidth"
        >
          <el-input
            type="textarea"
            v-model="editBaseinfo.description"
            autocomplete="off"
            placeholder="请输入项目描述"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="研究内容"
          :label-width="formLabelWidth"
          prop="details"
          :rules="[{ required: true, message: '请输入研究内容', trigger: 'blur' }]"
        >
          <el-input
            type="textarea"
            v-model="editBaseinfo.details"
            autocomplete="off"
            placeholder="请输入研究内容"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="submitBaseInfo('editBaseInfo')">确 定</el-button>
      </div>
    </el-dialog>

    <!-- 修改基本信息中表格弹框 -->
    <el-dialog
      title="项目组信息修改"
      :visible.sync="editBaseInfoTableDialog"
      class="addPlatform"
      width="760px"
      :close-on-click-modal="false"
    >
      <el-form :model="editBaseInfoTable" ref="editBaseInfoTable">
        <el-form-item
          label="项目组"
          label-width="100px"
          prop="name"
          :rules="[{ required: true, message: '请输入项目组名称', trigger: 'blur' }]"
        >
          <el-input v-model="editBaseInfoTable.name" autocomplete="off" placeholder="请输入项目组名称"></el-input>
        </el-form-item>
        <el-form-item
          label="样本量"
          label-width="100px"
          prop="sampleSize"
          :rules="[{ required: true, message: '请输入样本量', trigger: 'blur' }]"
        >
          <el-input
            v-model="editBaseInfoTable.sampleSize"
            autocomplete="off"
            placeholder="请输入样本量"
            type="number"
          ></el-input>
        </el-form-item>
        <el-form-item label="使用问卷计划" label-width="100px" prop="use">
          <el-select v-model="editBaseInfoTable.questionnairePlanId" placeholder="请选择要使用的使用问卷计划">
            <el-option v-for="(item, index) in surveyPlan" :key="index" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="submitBaseInfoTable('editBaseInfoTable')">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 增加项目组 -->
    <el-dialog
      title="添加项目组"
      :visible.sync="createGroupDialog"
      class="addPlatform"
      width="760px"
      :close-on-click-modal="false"
      destroy-on-close
    >
      <el-form :model="createGroup" ref="createGroup">
        <el-form-item
          label="项目组"
          label-width="100px"
          prop="name"
          :rules="[{ required: true, message: '请输入项目组名称', trigger: 'blur' }]"
        >
          <el-input v-model="createGroup.name" autocomplete="off" placeholder="请输入项目组名称"></el-input>
        </el-form-item>
        <el-form-item
          label="样本量"
          label-width="100px"
          prop="sampleSize"
          :rules="[{ required: true, message: '请输入样本量', trigger: 'blur' }]"
        >
          <el-input
            v-model="createGroup.sampleSize"
            autocomplete="off"
            placeholder="请输入样本量"
            type="number"
          ></el-input>
        </el-form-item>
        <el-form-item label="使用问卷计划" label-width="100px" prop="questionnairePlanId">
          <el-select v-model="createGroup.questionnairePlanId" placeholder="请选择要使用的使用问卷计划">
            <el-option v-for="(item, index) in surveyPlan" :key="index" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="submitCreateGroup('createGroup')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { updateProjectName,updateProjectGroup ,createGroup,getSurveyPlanList,fintProjectById} from '../service/getData'
export default {
  data() {
    return {
      surveyPlan:'',// 问卷计划列表
      tableData: [
        // {
        //   name: "实验组，对照组",
        //   sampleSize: "300",
        //   groupNum: "49",
        //   use: "真实世界研究"
        // },
      ],
      formLabelWidth: "80px",
      editBaseInfoDialog: false, // 修改基本信息的弹框
      editBaseInfoTableDialog: false, // 修改基本信息中项目组表格的弹框
      createGroupDialog : false,// 添加项目组弹框
      projectInfo:{},// 
      baseinfo: {
        // 展示基本信息
        // name: "", // 项目名称
        // type: "", // 项目类型
        // description: "", // 项目描述
        // details: "" // 研究内容
      },
      editBaseinfo:{
        // 修改基本信息表单
      },
      editBaseInfoTable: {
        // 修改基本信息项目组表格内容的表单填写项
        name: "", // 项目组名称
        sampleSize: "", // 样本量
        use: "", // 使用的逻辑
      },
      createGroup:{
        // "name": "测试组项目",
        // "projectId": 1,
        // "sampleSize": 600
      }
    };
  },
  methods: {
    // 提交修改基本信息的表单
    submitBaseInfo(ref) {
      this.$refs[ref].validate(valid => {
        if (valid) {
            this.handleUpdateBaseinfo(this.editBaseinfo);
        } else {
          return false;
        }
      });
    },
    // 提交修改基本信息表格内容的表单
    submitBaseInfoTable(ref) {
      this.$refs[ref].validate(valid => {
        if (valid) {
          let {id ,name,sampleSize,questionnairePlanId} = this.editBaseInfoTable;
          let params = {
            id,name,
            sampleSize,questionnairePlanId
          }
          this.handleUpdateProjectGroup(params);
        } else {
          return false;
        }
      });
    },
    // 创建项目组
    submitCreateGroup(ref){
      this.$refs[ref].validate(valid => {
        if (valid) {
          let projectId = this.projectInfo.id;
          let {name,sampleSize,questionnairePlanId} = this.createGroup;
          const params = {
            projectId,name,sampleSize,questionnairePlanId
          }
          this.handleCreateGroup(params);
          
        } else {
          return false;
        }
      });
    },
    // 基本信息
    editBaseinfoBtn(){ 
      this.editBaseinfo = _.cloneDeep(this.baseinfo);
      this.editBaseInfoDialog = true
    },
    // 项目组
    edit(row) {
      // console.log(row);
      let rowCopy = _.cloneDeep(row);
        if(rowCopy.questionnairePlan){
          rowCopy.questionnairePlanId = rowCopy.questionnairePlan.id;
        }
        this.editBaseInfoTable = rowCopy;
        this.editBaseInfoTableDialog = true;
    },
    // 根据id查找项目，更新项目信息
    async handleFindById(query){
      let res = await fintProjectById(query);
      this.$store.commit('setProjectInfo',res.data.data);
      this.projectInfo = res.data.data;
      let {  id,name,details,type,status,description } = this.projectInfo;
      this.baseinfo = {
        id,name,details,type,status,description
      }
      this.tableData = this.projectInfo.projectGroups;
      this.handleGetList({projectId : this.projectInfo.id});
    },
    // 修改项目名称，描述等
    async handleUpdateBaseinfo(params){
      let res = await updateProjectName(params);
      let { code, message } = res.data;
      if (code === 200) {
        this.$message({
          message: "修改成功",
          type: "success"
        });
        this.baseinfo = _.cloneDeep(params);
        Object.assign(this.projectInfo,params);//
        this.$store.commit('updateProjectInfo',params);
        this.editBaseInfoDialog = false;
        this.editBaseinfo = {};
      } else {
        this.$message.error(message);
      }
    },
    // 修改项目组
    async handleUpdateProjectGroup(params){
      let res = await updateProjectGroup(params);
      // console.log(res)
      let { code, message } = res.data;
      if(code == 200){
        this.$message({
          message: "修改成功",
          type: "success"
        });
        // this.$store.commit('updateProjectInfo',{projectGroups : this.tableData});
        this.handleFindById({id : this.baseinfo.id});
        this.editBaseInfoTableDialog = false;
      } else {
        this.$message.error(message);
      }
    },
    // 添加项目组
    async handleCreateGroup(params){
      let res = await createGroup(params)
      let { code, message,data } = res.data;
      if (code === 200) {
        this.$message({
          message: "添加成功",
          type: "success"
        });
        //// 可以创建完返回id
        let {id} = data;
        let {name,sampleSize,questionnairePlanId} = params;
        const newGroup = {
          name,sampleSize,id,questionnairePlanId
        }
        this.tableData.unshift(newGroup);
        // this.$store.commit('updateProjectInfo',{projectGroups : this.tableData});
        this.handleFindById({id : this.baseinfo.id});
        this.createGroupDialog = false;
        this.createGroup = {};
      } else {
        this.$message.error(message);
      }
    },
    // 获取项目下的问卷计划
    async handleGetList(query) {
      let res = await getSurveyPlanList(query);
      let {
        code,
        data,
        message
      } = res.data;
      if (code === 200) {
        this.surveyPlan = data;
      } else {
        this.$message.error(message);
      }
    },
  },
  created() {
    // console.log(this.$store.state.projectInfo);
    const projectInfo = this.$store.state.projectInfo;
    this.projectInfo = projectInfo;
    let {  id,name,details,type,status,description } = this.projectInfo;
    this.baseinfo = {
      id,name,details,type,status,description
    }
    this.tableData = this.projectInfo.projectGroups;
    this.handleGetList({projectId : projectInfo.id});
    // this.handleFindById({id : id})
  },
};
</script>
