<template>
  <!-- 项目详情页选项卡中问卷计划模块 -->
  <!-- 问卷计划 -->
  <div>
    <div class="pushPlan">
      <div class="control">
        <div style="font-weight:bold;font-size:16px;">问卷计划</div>
        <div>
          <el-button class="addBtn" @click="addPushplanDialog = true">添加问卷计划</el-button>
        </div>
      </div>
      <!-- <div class="title">项目组</div> -->
      <div class="table" style="margin-top:20px;">
        <el-table
          :data="tableData"
          style="width: 100%"
          border
          :header-cell-style="{background:'#F2F2F2',fontWeight:'normal'}"
        >
          <el-table-column prop="id" label="ID" width="80" align="center"></el-table-column>
          <el-table-column prop="name" label="问卷计划名称" width="150" align="center"></el-table-column>
          <el-table-column prop="users" label="使用人群" width="100" align="center"></el-table-column>
          <el-table-column prop="outGroupPrompt" label="出组提示语" width="150" align="center"></el-table-column>
          <el-table-column prop="duration" label="问卷持续天数" width="120" align="center"></el-table-column>
          <el-table-column prop="createTime" label="添加时间" width="120" align="center">
            <template slot-scope="scope">
              {{scope.row.createTime | parseTime}}
            </template>
          </el-table-column>
          <el-table-column prop="useStatus" label="状态" width="100" align="center">
            <template slot-scope="scope">
              {{scope.row.useStatus | parseUseStatus}}
            </template>
          </el-table-column>
          <el-table-column label="操作" width="280" align="center">
            <template slot-scope="scope" class="btns">
              <el-button @click.stop="handlePlan(scope.row)" size="mini" :disabled='scope.row.useStatus == 1'>启用计划</el-button>
              <el-button icon="el-icon-edit" @click.stop="edit(scope.row)" size="mini" :disabled='scope.row.useStatus == 1'>修改</el-button>
              <el-button icon="el-icon-delete" size="mini" @click.stop="delRow(scope.row)">删除</el-button>
              <a-icon type="right-circle" @click="toDaily(scope.row)" style="font-size:20px;color:#72B4DE;margin-left: 10px;vertical-align: middle;cursor:pointer;"/>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <!-- 添加问卷计划 -->
    <el-dialog
      title="添加问卷计划"
      :visible.sync="addPushplanDialog"
      class="addPlatform aduitDoc"
      width="760px"
      :close-on-click-modal="false"
      destroy-on-close	
    >
      <el-form :model="addPlan" ref="addPlan">
        <el-form-item
          label="问卷计划名称"
          :label-width="formLabelWidth"
          prop="name"
          :rules="[{ required: true, message: '请输入问卷计划名称', trigger: 'blur' }]"
        >
          <el-input v-model="addPlan.name" autocomplete="off" placeholder="请输入问卷计划名称"></el-input>
        </el-form-item>
        <el-form-item
          label="使用人群"
          :label-width="formLabelWidth"
          prop="users"
          :rules="{ required: true, message: '请选择使用人群', trigger: 'change' }"
        >
          <el-select v-model="addPlan.users" placeholder="请选择使用人群">
            <el-option label="患者" value="患者"></el-option>
            <el-option label="医生" value="医生"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label="出组提示语"
          :label-width="formLabelWidth"
          prop="outGroupPrompt"
          :rules="[{ required: true, message: '请输入出组提示语', trigger: 'blur' }]"
        >
          <el-input
            v-model="addPlan.outGroupPrompt"
            autocomplete="off"
            placeholder="请输入出组提示语"
          ></el-input>
        </el-form-item>
        
        <el-form-item
          label="问卷持续天数"
          :label-width="formLabelWidth"
          prop="duration"
          :rules="[{ required: true, message: '请输入问卷持续天数', trigger: 'blur' }]"
        >
          <el-input type='number' v-model="addPlan.duration" autocomplete="off" placeholder="请输入问卷持续天数"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="submitNewPlan('addPlan')">确 定</el-button>
      </div>
    </el-dialog>

    

    <!-- 修改问卷计划 -->
    <el-dialog
      title="修改问卷计划"
      :visible.sync="editPlanTableDialog"
      class="addPlatform"
      width="760px"
      :close-on-click-modal="false"
      destroy-on-close
    >
      <el-form :model="editPlanTable" ref="editPlanTable">
        <el-form-item
          label="问卷计划名称"
          :label-width="formLabelWidth"
          prop="name"
          :rules="[{ required: true, message: '请输入问卷计划名称', trigger: 'blur' }]"
        >
          <el-input v-model="editPlanTable.name" autocomplete="off" placeholder="请输入问卷计划名称"></el-input>
        </el-form-item>
        <el-form-item
          label="使用人群"
          :label-width="formLabelWidth"
          prop="users"
          :rules="{ required: true, message: '请选择使用人群', trigger: 'change' }"
        >
          <el-select v-model="editPlanTable.users" placeholder="请选择使用人群">
            <el-option label="患者" value="患者"></el-option>
            <el-option label="医生" value="医生"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label="出组提示语"
          :label-width="formLabelWidth"
          prop="outGroupPrompt"
          :rules="[{ required: true, message: '请输入出组提示语', trigger: 'blur' }]"
        >
          <el-input
            v-model="editPlanTable.outGroupPrompt"
            autocomplete="off"
            placeholder="请输入出组提示语"
          ></el-input>
        </el-form-item>
        
        <el-form-item
          label="问卷持续天数"
          :label-width="formLabelWidth"
          prop="duration"
          :rules="[{ required: true, message: '请输入问卷持续天数', trigger: 'blur' }]"
        >
          <el-input type='number' v-model="editPlanTable.duration" autocomplete="off" placeholder="请输入问卷持续天数"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="submitPlanTable('editPlanTable')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { createSurveyPlan ,getSurveyPlanList ,updateSurveyPlan} from '../service/getData'
import { parseTime2String } from '../tools/utils'
export default {
  data() {
    return {
      projectInfo:'',// 当前项目信息
      tableData: [
        {
          // id:'',
          // name:'问卷计划名称',
          // users:'使用人群',
          // outGroupPrompt:'出组提示语',
          // duration:'问卷持续天数',
          // createTime:'创建时间',
          // useStatus :'状态',
        },
      ],
      formLabelWidth: "120px",
      addPushplanDialog: false, // 添加任务的弹框
      editPlanTableDialog: false, // 修改问卷计划表格的弹框
      addPlan:{
        // 添加问卷计划
        // "duration":'问卷持续天数' ,
        // "name": "问卷名称",
        // "outGroupPrompt": "出组提示语",
        // "projectId": 项目id,
        useStatus:'',// 状态，已取消，传空值
        // "users": "使用人群"

      },
      editPlanTable: {
        // 修改问卷计划表格内容
        
      }
    };
  },
  methods: {
    // 添加问卷计划
    submitNewPlan(ref) {
      this.$refs[ref].validate(valid => {
        if (valid) {
          let projectId = this.projectInfo.id;
          const params = {...this.addPlan,projectId};
          this.handleCreate(params);
        } else {
          return false;
        }
      });
    },
    // 提交修改问卷计划的表单
    submitPlanTable(ref) {
      this.$refs[ref].validate(valid => {
        if (valid) {
          let {id,name,users,outGroupPrompt,duration} = this.editPlanTable;
          let params = {
            id,name,users,outGroupPrompt,duration
          }
         this.handleUpdate(params);
        } else {
          return false;
        }
      });
    },
    // 医生信息修改
    edit(row) {
      // console.log(row);
      this.editPlanTable = _.cloneDeep(row);
      this.editPlanTableDialog = true;
    },
    // 添加问卷计划
    async handleCreate(params){
      let res = await createSurveyPlan(params);
      let {
        code,
        data,
        message
      } = res.data;
      if(code === 200){
         this.$message({
          message: "添加成功",
          type: "success"
        });
        this.addPushplanDialog = false;
        this.handleGetList({projectId : params.projectId});
        this.addPlan = {
         useStatus : ''
        }
      } else {
        this.$message.error(message);
      }
    },
    // 修改问卷计划
    async handleUpdate(params){
      let res = await updateSurveyPlan(params);
      let {code,message} = res.data;
      if(code === 200){
         this.$message({
          message: "修改成功",
          type: "success"
        });
        this.editPlanTableDialog = false;
        this.handleGetList({projectId : this.projectInfo.id});
        this.editPlanTable = {}
      } else {
        this.$message.error(message);
      }
    },
    // 获取项目下的问卷计划列表
    async handleGetList(query) {
      let res = await getSurveyPlanList(query);
      let {
        code,
        data,
        message
      } = res.data;
      if (code === 200) {

        this.tableData = data;

      } else {
        this.$message.error(message);
      }
    },
    // 启用计划
    async handlePlan(row){
        this.$confirm('您是否已经把问卷计划全部设置完成，点击此按钮启动 后则不可进行修改。', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async () => {
          
          let res = await updateSurveyPlan({
            id : row.id,
            useStatus : 1
          });
          let {code,message} = res.data;
          if(code == 200){
            this.$message({
              type: 'success',
              message: '启用成功!'
            });
            this.handleGetList({projectId : this.projectInfo.id});
          } else {
              this.$message.error(message);
          }
          
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '您未启用计划'
          });          
        });
    },
    // 去往下一级：每日计划
    toDaily(row){
      // console.log(row);
      this.$router.push({path:'/home/projectDetail/dailyPlan',query:{questionnairePlanId : row.id,days:row.duration,pushtime:row.pushTime,useStatus:row.useStatus}});
    }
  },
  filters:{
    parseTime(timestamp){
     return parseTime2String(timestamp)
    },
    parseUseStatus(status){
      switch(status){
        case 0 :{
          return '未启用'
          break
        }
        case 1 :{
          return '已启用'
          break
        }
      }

    }
  },
  created() {
    // console.log(this.$store.state.projectInfo);
    const projectInfo = this.$store.state.projectInfo;
    this.projectInfo = projectInfo;
    this.handleGetList({projectId : projectInfo.id});
  },
};
</script>
