import { getAllHos, getAllDoc } from '../service/getData'
export const mixin = {
    // 获取所有医院和医生列表，用于下拉框选择
    data() {
        return {
            hosList: [],
            docList: [],
        }
    },
    methods: {
        async handleGetHos() {
            let list = await getAllHos();
            this.hosList = list.data.data.resultList;
        },
        async handleGetDoc() {
            let list = await getAllDoc();
            this.docList = list.data.data.resultList;
        }
    },
    created() {
        this.handleGetDoc();
        this.handleGetHos();
    },
}