<template>
  <!-- 医生管理 -->
  <div class="indexPage">
    <a-breadcrumb style="margin: 16px 0;padding-left:30px;" separator=">">
      <a-breadcrumb-item>
        <a-icon type="home" />首页
      </a-breadcrumb-item>
      <a-breadcrumb-item>医生管理</a-breadcrumb-item>
    </a-breadcrumb>
    <a-layout-content
      :style="{ background: '#fff', padding: '24px', margin: 0, minHeight: '280px',overflow:'scroll',height:'calc(100vh - 110px)'}"
    >
      <div class="control">
        <div class="searchGroup">
          <el-date-picker
            v-model="filterDate"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy-MM-dd"
            :editable='false'
            class="picker"
            unlink-panels
          ></el-date-picker>
          <el-input placeholder="按医生姓名查询" v-model="searchText" clearable class="searchInput"></el-input>
          <el-button class="searchBtn" @click="handleSearch">查询</el-button>
        </div>
        <el-button class="addBtn" @click="addDocDialog = true">添加医生</el-button>
      </div>
      <el-table
        :data="tableData"
        style="width: 100%"
        border
        :header-cell-style="{background:'#F2F2F2'}"
        v-loading="loading"
      >
        <el-table-column prop="id" label="编号" width="80" align="center"></el-table-column>
        <el-table-column prop="name" label="姓名" width="100" align="center"></el-table-column>
        <el-table-column prop="gender" label="性别" width="80" align="center">
          <template slot-scope="scope">
            {{scope.row.gender | parseGender}}
          </template>
        </el-table-column>
        <el-table-column prop="phoneNumber" label="手机号" width="120" align="center"></el-table-column>
        <el-table-column prop="hospitalName" label="医院" width="120" align="center"></el-table-column>
        <el-table-column prop="department" label="科室" width="90" align="center"></el-table-column>
        <el-table-column prop="title" label="职称" width="90" align="center"></el-table-column>
        <el-table-column prop="username" label="用户名" width="100" align="center"></el-table-column>
        <el-table-column prop="createTime" label="添加日期" width="120" align="center">
          <template slot-scope="scope">
            {{scope.row.createTime | parseTime}}
          </template>
        </el-table-column>
        <el-table-column prop="status" label="状态" width="100" align="center">
          <template slot-scope="scope">
            <span :style="{color:statusColor[scope.row.status]}">
              {{scope.row.status | showStatus}}
            </span>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="150" align="center">
          <template slot-scope="scope" class="btns">
            <el-button icon="el-icon-edit" @click.stop="edit(scope.row)" size="mini">修改</el-button>
            <el-button icon="el-icon-delete" size="mini" @click.stop="delRow(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
       background
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="pagesize"
        layout="prev, pager, next, jumper"
        :total="total"
        class="pages"
      ></el-pagination>
      <!-- 添加医生弹框 -->
      <el-dialog
        title="添加医生"
        :visible.sync="addDocDialog"
        class="addPlatform"
        width="760px"
        :close-on-click-modal="false"
      >
        <el-form :model="addDocForm" ref="addDocForm">
        <el-form-item
          label="医生姓名"
          :label-width="formLabelWidth"
          prop="name"
          :rules="[{ required: true, message: '请输入医生姓名', trigger: 'blur' }]"
        >
          <el-input v-model="addDocForm.name" autocomplete="off" placeholder="请输入医生姓名"></el-input>
        </el-form-item>
        <el-form-item
            label="性别"
            :label-width="formLabelWidth"
            prop="gender"
          >
            <el-radio-group v-model="addDocForm.gender">
              <el-radio label="0">男</el-radio>
              <el-radio label="1">女</el-radio>
            </el-radio-group>
          </el-form-item>
        <el-form-item
          label="手机号"
          :label-width="formLabelWidth"
          prop="phoneNumber"
          :rules="validatePhoneRules"
        >
          <el-input
            v-model="addDocForm.phoneNumber"
            autocomplete="off"
            placeholder="请输入手机号"
            type="number"
          ></el-input>
        </el-form-item>
        <el-form-item label="医院" :label-width="formLabelWidth" prop="hos" >
          <el-select v-model="addDocForm.hospital" placeholder="请选择所在医院">
            <el-option v-for="(item, index) in hosList" :key="index" :label="item.name" :value="item.name"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label="科室"
          :label-width="formLabelWidth"
          prop="department"
        >
          <el-input
            v-model="addDocForm.department"
            autocomplete="off"
            placeholder="请输入科室"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="职称"
          :label-width="formLabelWidth"
          prop="title"
        >
          <el-input
            v-model="addDocForm.title"
            autocomplete="off"
            placeholder="请输入职称"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="用户名"
          :label-width="formLabelWidth"
          prop="username"
          :rules="[{ required: true, message: '请输入用户名', trigger: 'blur' }]"
        >
          <el-input v-model="addDocForm.username" autocomplete="off" placeholder="请输入用户名"></el-input>
        </el-form-item>
        <div style='color:#CCCCCC;font-size:12px;margin-left:80px;'>注：用户名为医生登录后台时的用户名，密码默认为手机号后6位</div>
      </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="submitCreateDoc('addDocForm')">确 定</el-button>
        </div>
      </el-dialog>

      <!-- 修改信息 -->
      <el-dialog
        title="修改医生信息"
        :visible.sync="editDocDialog"
        class="addPlatform"
        width="760px"
        :close-on-click-modal="false"
      >
        <el-form :model="editform" ref="editform">
        <el-form-item
          label="医生姓名"
          :label-width="formLabelWidth"
          prop="name"
          :rules="[{ required: true, message: '请输入医生姓名', trigger: 'blur' }]"
        >
          <el-input v-model="editform.name" autocomplete="off" placeholder="请输入医生姓名"></el-input>
        </el-form-item>
        <el-form-item
            label="性别"
            :label-width="formLabelWidth"
            prop="gender"
          >
            <el-radio-group v-model="editform.gender">
              <el-radio :label="0">男</el-radio>
              <el-radio :label="1">女</el-radio>
            </el-radio-group>
          </el-form-item>
        <el-form-item
          label="手机号"
          :label-width="formLabelWidth"
          prop="phoneNumber"
          :rules="validatePhoneRules"
        >
          <el-input
            v-model="editform.phoneNumber"
            autocomplete="off"
            placeholder="请输入手机号"
            type="number"
          ></el-input>
        </el-form-item>
        <el-form-item label="医院" :label-width="formLabelWidth" prop="hos" >
          <el-select v-model="editform.hospitalName" placeholder="请选择所在医院">
            <el-option v-for="(item, index) in hosList" :key="index" :label="item.name" :value="item.name"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label="科室"
          :label-width="formLabelWidth"
          prop="department"
        >
          <el-input
            v-model="editform.department"
            autocomplete="off"
            placeholder="请输入科室"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="职称"
          :label-width="formLabelWidth"
          prop="title"
        >
          <el-input
            v-model="editform.title"
            autocomplete="off"
            placeholder="请输入职称"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="用户名"
          :label-width="formLabelWidth"
          prop="username"
          :rules="[{ required: true, message: '请输入用户名', trigger: 'blur' }]"
        >
          <el-input v-model="editform.username" autocomplete="off" placeholder="请输入用户名"></el-input>
        </el-form-item>
      </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="submitUpdate('editform')">确 定</el-button>
        </div>
      </el-dialog>
    </a-layout-content>
  </div>
</template>
<script>
import {mixin} from '../mixins/allDocAndPat'
import {createDoc ,getDocList,updateDoc} from '../service/getData'
import { parseTime2String } from '../tools/utils'
import validator from 'validator';
export default {
  mixins: [mixin],
  data() {
    var validatePhone = (rule, value, callback) => {
          if (!validator.isMobilePhone(value,'zh-CN')) {
            callback(new Error('请输入正确的手机号'));
          }  else {
            callback();
          }
      };
    return {
      statusColor:['#008DFF','#F81D22'],
      loading:false,
      searchText: "", // 输入的搜索内容
      filterDate: "", // 选择的开始结束日期
      addDocDialog: false, // 是否显示添加医生的弹框
      editDocDialog: false, // 是否显示编辑医生的弹框
      currentPage: 1, // 当前页
      pagesize: 15,
      total: 0, // 总条数
      addDocForm:{
        // 添加医生表单
        // "name": "医生姓名",
        // "username": "用户名",
        // "hospital": "医院",
        // "gender": 0,
        // "department": "科室",
        // "title": "职称",
        // "phoneNumber": "手机号"

      },
      editform: {
        // 编辑的item的数据
      },
      formLabelWidth: "80px",

      tableData: [],
      validatePhoneRules:[
        { required: true, message: '请输入手机号', trigger: 'blur' },
        { validator: validatePhone, trigger: 'blur'}
      ]
    };
  },
  methods: {
    // 提交创建医生
    submitCreateDoc(ref) {
      this.$refs[ref].validate(valid => {
        if (valid) {
          this.handleCreateDoc(this.addDocForm);
        } else {
          return false;
        }
      });
    },
    // 提交修改医生
    submitUpdate(ref) {
      this.$refs[ref].validate(valid => {
        if (valid) {
        let {id,name,username,hospital,gender,department,title,phoneNumber,hospitalName} = this.editform;
        let params = {
          id,name,username,gender,department,title,phoneNumber,
          hospital:hospitalName
        }
        this.handleUpdate(params);
        } else {
          return false;
        }
      });
    },
    // 修改
    edit(row) {
      // console.log(row);
      this.editform = _.cloneDeep(row);
      this.editDocDialog = true;
    },
    // 删除
    delRow(row) {
      // console.log(row);
    },
    handleCurrentChange(val) {
      const params = {
        size: this.pagesize,
        page: val,
        queryElement: {
          flag: 0,
          queryItems: []
        }
      };
      let queryItem = {};
      let queryItem2 = {};
      if (this.searchText.trim() != "") {
        queryItem = {
          columnName: "name",
          value: this.searchText,
          method: "like"
        };
        params.queryElement.queryItems.push(queryItem);
      }
      if(this.filterDate && this.filterDate.length != 0){
        queryItem2 = {
          columnName: "createTime",
          value:{
            "start": this.filterDate[0],
            "end": this.filterDate[1],
          },
          method: "between"
        };
        params.queryElement.queryItems.push(queryItem2);
      }
    this.handleGetList(params);
    },
    // 创建医生
    async handleCreateDoc(params){
      let res = await createDoc(params);
      let {
        code,
        data,
        message
      } = res.data;
      if(code === 200){
         this.$message({
          message: "添加成功",
          type: "success"
        });
        this.addDocDialog = false;
        const params = {
          size: this.pagesize,
          page: 1,
          queryElement: {
            flag: 0,
            queryItems: []
          }
        };
        this.handleGetList(params);
        this.addDocForm =  {}
      } else {
        this.$message.error(message);
      }
    },
    // 获取医生列表
    async handleGetList(params) {
      this.loading = true;
      let res = await getDocList(params);
      this.loading = false;
      let {
        code,
        data,
        data: { total, resultList },
        message
      } = res.data;
      if (code === 200) {
        this.total = total;
        resultList.forEach(item => {
            if(item.hospital){
              item.hospitalName = item.hospital.name
            }
        });
        this.tableData = resultList;
      } else {
        this.$message.error(message);
      }
    },
    // 修改医生
    async handleUpdate(params) {
      let res = await updateDoc(params);
      let { code, message } = res.data;
      if (code === 200) {
        this.$message({
          message: "修改成功",
          type: "success"
        });
        // let { id } = params;
        // 重新获取列表
        const params = {
          size: this.pagesize,
          page: this.currentPage,
          queryElement: {
            flag: 0,
            queryItems: []
          }
        };
        this.handleGetList(params);
        // this.tableData.some(item => {
        //   if (item.id == id) {
        //     Object.assign(item,params);
        //     // item.province = province;
        //     // item.city = city;
        //     // item.county = county;
        //     // item.detailAddress = detailAddress;
        //     // item.upHospital = upHospital;
        //     // item.principal = principal;
        //     // item.principalName = principalName;
        //     // item.address = address;
        //     return true;
        //   }
        // });
        this.editDocDialog = false;
        this.editform = {};
      } else {
        this.$message.error(message);
      }
    },
    // 搜索医生
    handleSearch() {
      const params = {
        size: this.pagesize,
        page: 1,
        queryElement: {
          flag: 0,
          queryItems: []
        }
      };
      let queryItem = {};
      let queryItem2 = {};
      if (this.searchText.trim() != "") {
        queryItem = {
          columnName: "name",
          value: this.searchText,
          method: "like"
        };
        params.queryElement.queryItems.push(queryItem);
      }
      if(this.filterDate && this.filterDate.length != 0){
        queryItem2 = {
          columnName: "createTime",
          value:{
            "start": this.filterDate[0],
            "end": this.filterDate[1],
          },
          method: "between"
        };
        params.queryElement.queryItems.push(queryItem2);
      }
      this.handleGetList(params);
    },
  },
  filters:{
    parseTime(timestamp){
     return parseTime2String(timestamp)
    },
    showStatus(status){
      switch(status){
        case 0 :{
          return '可用'
          break
        }
        case 1 :{
          return '不可用'
          break
        }
      }

    },
    parseGender(gender){
      switch(gender){
        case 0 : {
          return '男'
          break
        }
        case 1 :{
          return '女'
          break
        }
      }
    }
  },
  created() {
    const params = {
      size: this.pagesize,
      page: 1,
      queryElement: {
        flag: 0,
        queryItems: []
      }
    };
    this.handleGetList(params);
  }
};
</script>
