<template>
  <div class="login">
    <div class="content">
      <div class="image">
        <img src="../assets/loginPic.png" alt />
      </div>
      <div class="formGroup">
        <div class="title">项目管理平台登录</div>
        <div class="form">
          <a-form id="components-form-demo-normal-login" class="login-form">
            <a-form-item>
              <el-select v-model="type" placeholder="请选择身份" clearable>
                <el-option
                  v-for="item in usertype"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </a-form-item>
            <a-form-item>
              <a-input placeholder="请输入账号或用户名" v-model="username" @keydown.enter="login">
                <a-icon slot="prefix" type="user" style="color: rgba(0,0,0,.25)" />
              </a-input>
            </a-form-item>
            <a-form-item>
              <a-input-password placeholder="请输入密码" v-model="pwd" @keydown.enter="login">
                <a-icon slot="prefix" type="lock" style="color: rgba(0,0,0,.25)" />
              </a-input-password>
            </a-form-item>
            <a-form-item class="subBtn">
              <a-button type="primary" class="login-form-button" block @click="login">登录</a-button>
            </a-form-item>
          </a-form>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { handleLogin } from '../service/getData'
export default {
  data() {
    return {
      usertype:[
        {label:'超级管理员',value:'root'},
        {label:'管理员',value:'doctor'}
      ],
      type:'',// 用户类型
      username: "",
      pwd: ""
    };
  },
  methods: {
    // validate username and pwd
    async login() {
      if(this.type.trim() == ''){
        this.$message.warning("请选择身份", 1);
        return;
      }
      if (this.username.trim() == "") {
        this.$message.warning("请输入用户名", 1);
        this.pwd = '';
        return;
      }
      if (this.pwd.trim() == "") {
        this.$message.warning("请输入密码", 1);
        return;
      }
      const params = {
        usertype:this.type,
        username:this.username,
        password:this.pwd
      }
      let res = await handleLogin(params);
      let {code,data,data:{role,username},token,message} = res.data;
      if(code === 200){
        this.$store.commit('setLoginInfo',data);
        this.$store.commit('setToken',token);
        this.$message.success('登录成功',1,);
        this.$router.replace('/home')
      } else {
        this.$message.error(message);
        this.pwd = '';
      }
    }
  }
};
</script>
