        <el-table-column prop="type" label="类型" width="120" align="center"></el-table-column>
        <el-table-column prop="name" label="项目名称" width="120" align="center">
          <template slot-scope="scope">
            <div class="rowcontent" style="display: inline-block;overflow:hidden;white-space:nowrap;text-overflow:ellipsis;width:80%;float:left;">{{scope.row.name}}</div>
            <div class="qrcode" style="display:inline-block;cursor:pointer;float:right;" @click.stop='showQrCode(scope.row)'>
              <img src="../assets/qrcode.png" alt="">
