        </el-table-column>
        <el-table-column prop="details" label="研究内容" width="200" align="center"></el-table-column>
        <el-table-column prop="groupText" label="项目组" width="120" align="center"></el-table-column>
        <el-table-column prop="createTime" label="添加日期" width="120" align="center">
          <template slot-scope="scope">
            {{scope.row.createTime | parseTime}}
          </template>
        </el-table-column>
        <el-table-column prop="questionSize" label="问卷数" width="80" align="center"></el-table-column>
        <el-table-column prop="doctorSize" label="医生数" width="80" align="center"></el-table-column>
        <el-table-column prop="recruitedPatients" label="患者数" width="80" align="center"></el-table-column>
        <el-table-column prop="status" label="状态" width="80" align="center">
          <template slot-scope="scope">
            <span :style="{color:statusColor[scope.row.status]}">
              {{scope.row.status | showStatus}}
            </span>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="200" align="center">
          <template slot-scope="scope" class="btns">
            <el-button icon="el-icon-edit" @click.stop="edit(scope.row)" size="mini">修改</el-button>
            <el-button icon="el-icon-delete" size="mini" @click.stop="delRow(scope.row)">删除</el-button>
            <a-icon type="right-circle" @click="rowClick(scope.row)" style="font-size:20px;color:#72B4DE;margin-left: 10px;vertical-align: middle;cursor:pointer;"/>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
         background
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="pagesize"
        layout="prev, pager, next, jumper"
        :total="total"
        class="pages"
      ></el-pagination>
      <!-- 添加项目弹框 -->
      <el-dialog
        title="添加项目"
        :visible.sync="addProjectDialog"
        class="addPlatform"
        width="760px"
        :close-on-click-modal="false"
      >
        <el-form :model="addProform" ref="addProform">
          <el-form-item
            label="项目类型"
            :label-width="formLabelWidth"
            prop="type"
            :rules="{ required: true, message: '请选择项目类型', trigger: 'change' }"
          >
            <el-select v-model="addProform.type" placeholder="请选择项目类型">
              <el-option label="真实世界研究" value="真实世界研究"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item
            label="项目名称"
            :label-width="formLabelWidth"
            prop="name"
            :rules="[{ required: true, message: '请输入项目名称', trigger: 'blur' }]"
          >
            <el-input v-model="addProform.name" autocomplete="off" placeholder='请输入项目名称'></el-input>
          </el-form-item>
          <el-form-item
            label="项目描述"
            :label-width="formLabelWidth"
          >
            <el-input type='textarea' v-model="addProform.description" autocomplete="off"  placeholder="请输入项目描述"></el-input>
          </el-form-item>
          <el-form-item
            label="研究内容"
            :label-width="formLabelWidth"
            prop="details"
            :rules="[{ required: true, message: '请输入研究内容', trigger: 'blur' }]"
          >
            <el-input type='textarea' v-model="addProform.details" autocomplete="off"  placeholder="请输入研究内容"></el-input>
          </el-form-item>
          <el-form-item
            label="项目组"
            :label-width="formLabelWidth"
            :rules="[{ required: true}]"
          >
            <el-col :span="11">
                <el-form-item prop="group1Name" :rules="[{ required: true, message: '请输入项目组名称',trigger: 'blur' }]">
                    <el-input v-model="addProform.group1Name" autocomplete="off" class="groupNameInput" placeholder='请输入项目组名称'></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="10">
                <el-form-item  prop="group1Count" :rules="[{ required: true, message: '请输入样本量',trigger: 'blur' }]">
                    <label>样本量:</label><el-input v-model="addProform.group1Count" autocomplete="off" class="groupCountInput" type='number' placeholder='请输入样本量'></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="3">
                <i class="el-icon-circle-plus-outline" @click="increament"></i>
                <!-- <i class="el-icon-remove-outline"></i> -->
            </el-col>
          </el-form-item>
          <!-- 动态添加的项目组item -->
          <el-form-item
            label=""
            :label-width="formLabelWidth"
            v-for="(item, index) in addProform.groupArr" :key="index"
          >
            <el-col :span="11">
                <el-form-item prop="group1Name">
                    <el-input v-model="item.name" autocomplete="off" class="groupNameInput" placeholder='请输入项目组名称'></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="10">
                <el-form-item  prop="group1Count">
                    <label>样本量:</label><el-input v-model="item.sampleSize" autocomplete="off" class="groupCountInput" type='number' placeholder='请输入样本量'></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="3">
                <i class="el-icon-circle-plus-outline" @click="increament"></i>
                <i class="el-icon-remove-outline" @click="decrement(index)"></i>
            </el-col>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="submitCreate('addProform')">确 定</el-button>
        </div>
      </el-dialog>
      <!-- 编辑信息 -->
      <el-dialog
        title="修改项目"
        :visible.sync="editProjectDialog"
        class="addPlatform"
        width="760px"
        :close-on-click-modal="false"
      >
        <el-form :model="editform" ref="editform">
          <el-form-item
            label="项目类型"
            :label-width="formLabelWidth"
            prop="type"
            :rules="{ required: true, message: '请选择项目类型', trigger: 'change' }"
          >
            <el-select v-model="editform.type" placeholder="请选择项目类型">
              <el-option label="真实世界研究" value="真实世界研究"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item
            label="项目名称"
            :label-width="formLabelWidth"
            prop="name"
            :rules="[{ required: true, message: '请输入项目名称', trigger: 'blur' }]"
          >
            <el-input v-model="editform.name" autocomplete="off" placeholder='请输入项目名称'></el-input>
          </el-form-item>
          <el-form-item
            label="项目描述"
            :label-width="formLabelWidth"
          >
            <el-input type='textarea' v-model="editform.description" autocomplete="off"  placeholder="请输入项目描述"></el-input>
          </el-form-item>
          <el-form-item
            label="研究内容"
            :label-width="formLabelWidth"
            prop="details"
            :rules="[{ required: true, message: '请输入研究内容', trigger: 'blur' }]"
          >
            <el-input type='textarea' v-model="editform.details" autocomplete="off"  placeholder="请输入研究内容"></el-input>
          </el-form-item>
          <!-- <el-form-item
            label="项目组"
            :label-width="formLabelWidth"
            :rules="[{ required: true}]"
            v-for="(item, index) in editform.projectGroups" :key="index"
          >
            <el-col :span="11">
                <el-form-item>
                    <el-input v-model="item.name" autocomplete="off" class="groupNameInput" placeholder='请输入项目组名称'></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="10">
                <el-form-item >
                    <label>样本量:</label><el-input v-model="item.sampleSize" autocomplete="off" class="groupCountInput" type='number' placeholder='请输入样本量'></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="3">
                <i class="el-icon-circle-plus-outline"></i>
                <i class="el-icon-remove-outline"></i>
            </el-col>
          </el-form-item> -->
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="submitUpdate('editform')">确 定</el-button>
        </div>
      </el-dialog>

      <!-- 展示项目二维码 -->
      <el-dialog
        title="项目二维码"
        :visible.sync="QrcodeDialog"
        class="addPlatform"
        width="760px"
        :close-on-click-modal="false"
        @close='destroyQrcode'
        top='25vh'
      >
        <div class="qrcodeBox">
          <div class="qrcodeContainer">
            <span class="tipText">正在加载二维码...</span>
            <img :src="qrCode">
          </div>
        </div>
        <div slot="footer" class="dialog-footer">
          <el-button @click="QrcodeDialog = false">确 定</el-button>
        </div>
      </el-dialog>
