<script>
import { createProject , getProjectList , updateProjectName } from '../service/getData'
import { parseTime2String } from '../tools/utils'
export default {
  data() {
    return {
      statusColor:['#4DC600','#008DFF','#FCAE00','blue'],
      loading:false,
      searchText: "", // 输入的搜索内容
      filterDate:'',// 选择的开始结束日期
      addProjectDialog: false, // 是否显示添加平台的弹框
      editProjectDialog: false, // 是否显示编辑平台的弹框
      QrcodeDialog:false,// 二维码弹框
      currentPage: 1, // 当前页
      pagesize: 15,
      total: 0, // 总条数
      qrCode:'',//二维码地址
      addProform: {
        // 添加项目的表单填写项
        name: "", // 项目名称
        group1Name: "", // 至少存在的项目组名称
        group1Count: "", // 至少存在的项目组样本量
        groupArr:[ // 可添加的项目组
            // {name:'',simpleSize:''},
        ],
        type: "", // 项目类型
        status : 0, // 项目状态
        details: "", // 研究内容
        description :'',// 项目描述
      },
      editform: {
        // 编辑的item的数据
      },
      formLabelWidth: "80px",

      tableData: [
        // {
        //   name: "水解蛋白研究项目组",
        //   groupText: "实验组，对照组",
        //   type: "真实世界研究",
        //  
        // },
      ]
    };
  },
  methods: {
    // 提交创建
    submitCreate(ref) {
      this.$refs[ref].validate(valid => {
        if (valid) {
          let flag = this.addProform.groupArr.some(item => {
          if(item.sampleSize == '' || item.name == ''){
              this.$message({
                message: '您还有未填写完的项目组信息',
                type: 'warning'
              });
            return true;
            }
          });
        if(flag){
          return;
        }
        let {name,group1Name,group1Count,groupArr,type,status,details,description} = this.addProform;
        let temObj = {name:group1Name,sampleSize:group1Count};
        let params = {
          name,
          details,
          type,
          status,
          description,
          projectGroups:[temObj,...groupArr,]
        };
        this.handleCreate(params)
        } else {
          return false;
        }
      });
    },
    // 提交修改
    submitUpdate(ref) {
      this.$refs[ref].validate(valid => {
        if (valid) {
          let { id,name,details,type,status,description} = this.editform;
          const params = {
            id,name,details,type,status,description
          }
          this.handleUpdate(params);
        } else {
          return false;
        }
      });
    },
    // 修改
    edit(row) {
      // console.log(row);
      this.editform = row;
      this.editProjectDialog = true;
    },
    // 删除
    delRow(row) {
      // console.log(row);
    },
    handleCurrentChange(val) {
      const params = {
        size: this.pagesize,
        page: val,
        queryElement: {
          flag: 0,
          queryItems: []
        }
      };
      let queryItem = {};
      let queryItem2 = {};
      if (this.searchText.trim() != "") {
        queryItem = {
          columnName: "name",
          value: this.searchText,
          method: "like"
        };
        params.queryElement.queryItems.push(queryItem);
      }
      if(this.filterDate && this.filterDate.length != 0){
        queryItem2 = {
          columnName: "createTime",
          value:{
            "start": this.filterDate[0],
            "end": this.filterDate[1],
          },
          method: "between"
        };
        params.queryElement.queryItems.push(queryItem2);
      }
    this.handleGetList(params);
    },
    // 创建项目以及项目组
    async handleCreate(params){
      let res = await createProject(params);
      // console.log(res.data)
      let {
        code,
        data,
        message
      } = res.data;
      if(code === 200){
         this.$message({
          message: "添加成功",
          type: "success"
        });
        this.addProjectDialog = false;
        const params = {
          size: this.pagesize,
          page: 1,
          queryElement: {
            flag: 0,
            queryItems: []
          }
        };
        this.handleGetList(params);
        this.selectedOptions = [];
        this.addProform = {
          name: "", 
          group1Name: "",
          group1Count: "",
          groupArr:[],
          type: "",
          status : 0,
          details: "",
          description :'',
        }
      } else {
        this.$message.error(message);
      }
    },
    // 获取项目列表
    async handleGetList(params) {
      this.loading = true;
      let res = await getProjectList(params);
      this.loading = false;
      let {
        code,
        data,
        data: { total, resultList },
        message
      } = res.data;
      if (code === 200) {
        
        resultList.forEach(item => {
          let { projectGroups } = item;
          let groupText = projectGroups.reduce((group,current) => {
            return `${group},${current.name}`
          },'');
          item.groupText = groupText.substring(1);
        });
        this.total = total;
        this.tableData = resultList;

      } else {
        this.$message.error(message);
      }
    },
    // 修改项目名称，描述等
    async handleUpdate(params){
      let res = await updateProjectName(params);
      let { code, message } = res.data;
      if (code === 200) {
        this.$message({
          message: "修改成功",
          type: "success"
        });
        let { id,name,details,type,status,description } = params;
        let principalName;
        this.tableData.some(item => {
          if (item.id == id) {
            item.name = name;
            item.details = details;
            item.type = type;
            item.description = description;
            return true;
          }
        });
        this.editProjectDialog = false;
        this.editform = {};
      } else {
        this.$message.error(message);
      }
    },
    // 搜索项目
    handleSearch() {
      const params = {
        size: this.pagesize,
        page: 1,
        queryElement: {
          flag: 0,
          queryItems: []
        }
      };
      let queryItem = {};
      let queryItem2 = {};
      if (this.searchText.trim() != "") {
        queryItem = {
          columnName: "name",
          value: this.searchText,
          method: "like"
        };
        params.queryElement.queryItems.push(queryItem);
      }
      if(this.filterDate && this.filterDate.length != 0){
        queryItem2 = {
          columnName: "createTime",
          value:{
            "start": this.filterDate[0],
            "end": this.filterDate[1],
          },
          method: "between"
        };
        params.queryElement.queryItems.push(queryItem2);
      }
      this.handleGetList(params);
    },
    // 点击行，进入详情
    rowClick(info){
        this.$store.commit('setProjectInfo',info)
        this.$router.push('/home/projectDetail')
    },
    // 显示二维码
    showQrCode(row){
      this.qrCode = row.qrCode;
      this.QrcodeDialog = true;
    },
    // 关闭时销毁二维码的连接
    destroyQrcode(){
      this.qrCode = '';
    },
    // 增减项目组
    increament(){
      if(this.addProform.group1Name == '' || this.addProform.group1Count == ''){
        this.$message({
          message: '您还有信息没有填写完整',
          type: 'warning'
        });
        return;
      }
      let flag = this.addProform.groupArr.some(item => {
        if(item.sampleSize == '' || item.name == ''){
          this.$message({
          message: '您还有信息没有填写完整',
          type: 'warning'
        });
        return true;
        }
      });
      if(flag){
        return;
      }
      const obj = {name:'',sampleSize:''};
      this.addProform.groupArr.push(obj);
      },
    decrement(index){
      this.addProform.groupArr.splice(index,1);
    }
  },
  filters:{
    parseTime(timestamp){
     return parseTime2String(timestamp)
    },
    showStatus(status){
      switch(status){
        case 0 :{
          return '未开始'
          break
        }
        case 1 :{
          return '进行中'
          break
        }
        case 2 :{
          return '已完成'
          break
        }
      }

    }
  },
  created() {
    const params = {
      size: this.pagesize,
      page: 1,
      queryElement: {
        flag: 0,
        queryItems: []
      }
    };
    this.handleGetList(params);
  }
    
  }
</script>
