import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        loginInfo: JSON.parse(sessionStorage.getItem('LOGININFO')) || {}, // login success userinfo
        token: sessionStorage.getItem('LOGINSUCCTOKE') || '', // token
        projectInfo: JSON.parse(sessionStorage.getItem('PROJECTINFO')) || {},
    },
    mutations: {
        setLoginInfo(state, data) {
            sessionStorage.setItem('LOGININFO', JSON.stringify(data));
            state.loginInfo = data;
        },
        setToken(state, token) {
            sessionStorage.setItem('LOGINSUCCTOKE', token);
            state.token = token;
        },
        setProjectInfo(state, info) {
            sessionStorage.setItem('PROJECTINFO', JSON.stringify(info));
            state.projectInfo = info;
        },
        //详情页修改后更新源数据
        updateProjectInfo(state, newdData) {
            const info = JSON.parse(sessionStorage.getItem('PROJECTINFO'));
            Object.assign(info, newdData);
            sessionStorage.setItem('PROJECTINFO', JSON.stringify(info));
            state.projectInfo = info;
        }
    },
    actions: {},
    modules: {}
})