import axios from 'axios'
import router from '../router'


const request = axios.create({
    // baseURL: 'http://47.90.243.20:8002',
    baseURL: 'http://60.214.101.201:8989',
    // baseURL: 'http://192.168.2.9:8002',
});

request.interceptors.request.use(function(config) {
    // console.log(config)
    let token = sessionStorage.getItem('LOGINSUCCTOKE');
    config.headers.Token = token;

    return config　　
}, function(error) {　 // 对请求错误
    return Promise.reject(error)
});

// 添加响应拦截器
request.interceptors.response.use(function(response) {
    // console.log(response);


    return response　
}, function(error) {
    if (error.response.status === 500 && error.response.data.message.includes('Token失效')) {
        //  说明 token 验证失败
        // 可以直接跳转到登录页面，重新登录获取 token
        sessionStorage.clear();
        router.replace('/login');
    } else {　
        return Promise.reject(error)
    }
    return Promise.reject(error.response.data)
});

export default async(type = 'GET', url = '', data = {}) => {
    type = type.toLowerCase()
    if (type === 'get') {
        return request({
            url: url,
            method: type,
            params: data
        })
    } else {
        return request({
            url: url,
            method: type,
            data: data
        })
    }
}