<script>
import topHeader from "../components/topHeader";
import { mapState } from 'vuex';
export default {
  data() {
    return {
      defaultSelectedKeys:[]
    };
  },
  methods: {
      routeTo({ key }){
          this.$router.push(key)
      }
  },
  computed: {
    ...mapState({
      role : state => state.loginInfo.role
    })
  },
  watch: {
    '$route':function(newval){
      let path = newval.path;
      
      this.defaultSelectedKeys.splice(0,1,path);

    }
  },
  components: {
    topHeader
  },
  created() {
    let path = this.$route.path;
    if(path.includes('/home/projectDetail')){
        path = '/home/project'
    }
    this.defaultSelectedKeys.splice(0,1,path);

  },
};
</script>

