<template>
  <!-- 平台管理 -->
  <div class="indexPage">
    <a-breadcrumb style="margin: 16px 0;padding-left:30px;" separator=">">
      <a-breadcrumb-item>
        <a-icon type="home" />首页
      </a-breadcrumb-item>
      <a-breadcrumb-item>平台管理</a-breadcrumb-item>
    </a-breadcrumb>
    <a-layout-content
      :style="{ background: '#fff', padding: '24px', margin: 0, minHeight: '280px',overflow:'scroll',height:'calc(100vh - 110px)'}"
    >
      <div class="control">
        <div class="searchGroup">
          <el-input placeholder="按姓名或平台名称查询" v-model="searchText" clearable class="searchInput" @clear='handleSearch' @change='handleSearch'></el-input>
          <el-button class="searchBtn" @click="handleSearch">查询</el-button>
        </div>
        <el-button class="addBtn" @click="addPlatformDialog = true">添加平台</el-button>
      </div>
      <el-table
        :data="tableData"
        style="width: 100%"
        border
        :header-cell-style="{background:'#F2F2F2'}"
        v-loading="loading"
      >
        <el-table-column prop="id" label="ID" width="80" align="center"></el-table-column>
        <el-table-column prop="name" label="姓名" width="80" align="center"></el-table-column>
        <el-table-column prop="gender" label="性别" width="80" align="center">
          <template slot-scope="scope">
              {{scope.row.gender | parseGender}}
          </template>
        </el-table-column>
        <el-table-column prop="phoneNumber" label="手机号" width="150" align="center"></el-table-column>
        <el-table-column prop="hospital" label="医院" width="200" align="center"></el-table-column>
        <el-table-column prop="department" label="科室" width="100" align="center"></el-table-column>
        <el-table-column prop="scenarioName" label="平台名称" width="200" align="center"></el-table-column>
        <el-table-column prop="scenarioDescription" label="平台描述" width="100" align="center"></el-table-column>
        <el-table-column label="操作" width="150" align="center">
          <template slot-scope="scope" class="btns">
            <el-button icon="el-icon-edit" @click="edit(scope.row)" size="mini">修改</el-button>
            <el-button icon="el-icon-delete" size="mini" @click="delRow(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        background
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="pagesize"
        layout="prev, pager, next, jumper"
        :total="total"
        class="pages"
      ></el-pagination>
      <!-- 添加平台弹框 -->
      <el-dialog
        title="添加平台"
        :visible.sync="addPlatformDialog"
        class="addPlatform"
        width="760px"
        :close-on-click-modal="false"
        destroy-on-close
      >
        <el-form :model="addPlatform" ref="addPlatform">
          <el-form-item
            label="医生姓名"
            :label-width="formLabelWidth"
            prop="name"
            :rules="[{ required: true, message: '请输入姓名', trigger: 'blur' }]"
          >
            <el-input v-model="addPlatform.name" autocomplete="off" placeholder="请输入医生姓名"></el-input>
          </el-form-item>
          <el-form-item
            label="性别"
            :label-width="formLabelWidth"
            prop="gender"
            :rules="[{ required: true, message: '请选择性别',trigger: 'change' }]"
          >
            <el-radio-group v-model="addPlatform.gender">
              <el-radio label="0">男</el-radio>
              <el-radio label="1">女</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item
            label="手机号"
            :label-width="formLabelWidth"
            prop="phoneNumber"
            :rules="validatePhoneRules"
          >
            <el-input
              v-model="addPlatform.phoneNumber"
              autocomplete="off"
              type="number"
              placeholder="请输入手机号码"
            ></el-input>
          </el-form-item>
          <el-form-item
            label="医院"
            :label-width="formLabelWidth"
            prop="hospital"
            :rules="{ required: true, message: '请选择医院', trigger: 'change' }"
          >
            <el-select v-model="addPlatform.hospital" placeholder="请选择医院">
              <el-option
                v-for="(item, index) in hosList"
                :key="index"
                :label="item.name"
                :value="item.name"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item
            label="科室"
            :label-width="formLabelWidth"
            prop="department"
            :rules="[{ required: true, message: '请输入科室', trigger: 'blur' }]"
          >
            <el-input v-model="addPlatform.department" autocomplete="off" placeholder="请输入科室名称"></el-input>
          </el-form-item>
          <el-form-item
            label="平台名称"
            :label-width="formLabelWidth"
            prop="scenarioName"
            :rules="[{ required: true, message: '请输入平台名称', trigger: 'blur' }]"
          >
            <el-input v-model="addPlatform.scenarioName" autocomplete="off" placeholder="请输入平台名称"></el-input>
          </el-form-item>
          <el-form-item label="平台描述" :label-width="formLabelWidth" prop="scenarioDescription">
            <el-input
              type="textarea"
              v-model="addPlatform.scenarioDescription"
              autocomplete="off"
              placeholder="请输入平台描述"
            ></el-input>
          </el-form-item>
          <el-form-item
            label="用户名"
            :label-width="formLabelWidth"
            prop="username"
            :rules="[{ required: true, message: '请输入用户名', trigger: 'blur' }]"
          >
            <el-input v-model="addPlatform.username" autocomplete="off" placeholder="请输入用户名"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="submitCreate('addPlatform')">确 定</el-button>
        </div>
      </el-dialog>
      <!-- 编辑信息 -->
      <el-dialog
        title="修改信息"
        :visible.sync="editPlatformDialog"
        class="addPlatform"
        width="760px"
        :close-on-click-modal="false"
        destroy-on-close
      >
        <el-form :model="editPlatform" ref="editPlatform">
          <el-form-item
            label="平台名称"
            :label-width="formLabelWidth"
            prop="name"
            :rules="[{ required: true, message: '请输入平台名称', trigger: 'blur' }]"
          >
            <el-input v-model="editPlatform.name" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="平台描述" :label-width="formLabelWidth">
            <el-input
              type="textarea"
              v-model="editPlatform.description"
              autocomplete="off"
              placeholder="请输入平台描述"
            ></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="submitUpdate('editPlatform')">确 定</el-button>
        </div>
      </el-dialog>
    </a-layout-content>
  </div>
</template>
<script>
// import { mapState } from 'vuex';
import { mixin } from "../mixins/allDocAndPat";
import {
  createPlatform,
  getPlatformList,
  updatePlatform
} from "../service/getData";
import validator from 'validator';
export default {
  mixins: [mixin],
  data() {
    var validatePhone = (rule, value, callback) => {
      if (!validator.isMobilePhone(value,'zh-CN')) {
        callback(new Error('请输入正确的手机号'));
      }  else {
        callback();
      }
    };
    return {
      loading:false,
      searchText: "", // 输入的搜索内容
      addPlatformDialog: false, // 是否显示添加平台的弹框
      editPlatformDialog: false, // 是否显示编辑平台的弹框
      currentPage: 1, // 当前页
      pagesize: 15,
      total: 0, // 总条数
      addPlatform: {
        // 添加平台的表单填写项
        name: "",
        gender: "",
        phoneNumber: "",
        hospital: "",
        department: "",
        scenarioName: "",
        username: "",
        scenarioDescription: "",
        role: "admin" // 暂时是固定的admin，日后角色多了可能会改
      },
      editPlatform: {
        // 编辑的item的数据
      },
      formLabelWidth: "80px",

      tableData: [
        // {
        //   id:12,
        //   name: "啊啊啊",
        //   gender: "1",
        //   phoneNumber: "13121182569",
        //   hospital: "首都医科大学附属医院",
        //   department: "感染科",
        //   scenarioName: "平台名称",
        //   scenarioDescription: "平台描述",
        // },
      ],
      validatePhoneRules:[
        { required: true, message: '请输入手机号', trigger: 'blur' },
        { validator: validatePhone, trigger: 'blur'}
      ]
    };
  },
  methods: {
    // 提交表单 --添加平台
    submitCreate(ref) {
      this.$refs[ref].validate(valid => {
        if (valid) {
          // 提交表单
          this.handleCreate(this.addPlatform);
        } else {
          return false;
        }
      });
    },
    // 提交表单 --修改平台
    submitUpdate(ref) {
      this.$refs[ref].validate(valid => {
        if (valid) {
          // 提交表单
          this.handleUpdate(this.editPlatform);
        } else {
          return false;
        }
      });
    },
    // 修改
    edit(row) {
      let { id, principal, scenarioName, scenarioDescription } = row;
      this.editPlatform = {
        id,
        name: scenarioName,
        description: scenarioDescription,
        principalId: principal
      };
      this.editPlatformDialog = true;
    },
    // 删除
    delRow(row) {
      // console.log(row);
    },
    // 添加平台
    async handleCreate(params) {
      let res = await createPlatform(params);
      let {
        code,
        data,
        message
      } = res.data;
      if (code === 200) {
        this.$message({
          message: "添加成功",
          type: "success"
        });
        this.addPlatformDialog = false;
        const params = {
          size: this.pagesize,
          page: 1,
          queryElement: {
            flag: 0,
            queryItems: []
          }
        };
        this.handleGetList(params);
        this.addPlatform = {
          name: "",
          gender: "",
          phoneNumber: "",
          hospital: "",
          department: "",
          scenarioName: "",
          username: "",
          scenarioDescription: "",
          role: "admin"
        };
      } else {
        this.$message.error(message);
      }
    },
    // 获取平台列表
    async handleGetList(params) {
      this.loading = true;
      let res = await getPlatformList(params);
      this.loading = false;
      let {
        code,
        data,
        data: { total, resultList },
        message
      } = res.data;
      if (code === 200) {
        this.total = total;
        this.tableData = resultList;
      } else {
        this.$message.error(message);
      }
    },
    // 修改平台
    async handleUpdate(params) {
      let res = await updatePlatform(params);
      let { code, message } = res.data;
      if (code === 200) {
        this.$message({
          message: "修改成功",
          type: "success"
        });
        let { id, name, description } = params;
        this.tableData.some(item => {
          if (item.id == id) {
            item.scenarioName = name;
            item.scenarioDescription = description;
            return true;
          }
        });
        this.editPlatformDialog = false;
      } else {
        this.$message.error(message);
      }
    },
    // 页码改变
    handleCurrentChange(val) {
      this.currentPage = val;
      const params = {
        size: this.pagesize,
        page: val,
        queryElement: {
          flag: 0,
          queryItems: []
        }
      };
      let queryItem = {};
      if (this.searchText.trim() != "") {
        queryItem = {
          columnName: "name",
          value: this.searchText,
          method: "like"
        };
        params.queryElement.queryItems.push(queryItem);
      }
      this.handleGetList(params);
    },
    // 搜索平台
    handleSearch() {
      const params = {
        size: this.pagesize,
        page: 1,
        queryElement: {
          flag: 0,
          queryItems: []
        }
      };
      let queryItem = {};
      if (this.searchText.trim() != "") {
        queryItem = {
          columnName: "name",
          value: this.searchText,
          method: "like"
        };
        params.queryElement.queryItems.push(queryItem);
      }
      this.handleGetList(params);
    }
  },
  filters:{
    parseGender(gender){
      return gender == 0 ? '男' : '女'
    }
  },
  created() {
    const params = {
      size: this.pagesize,
      page: 1,
      queryElement: {
        flag: 0,
        queryItems: []
      }
    };
    this.handleGetList(params);
  }
};
</script>
