<script>
import { getTaskList,createTask,getAllSurvey ,updateTask} from '../service/getData'
export default {
  data() {
    return {
      useStatus :'',// 启用状态
      questionnairePlanItemId : '', // 问卷计划项id
      currentDay : '',
      duration:'',// 问卷持续天数
      allSurvey:'',// 问卷列表
      tableData: [],
      formLabelWidth: "120px",
      addTaskDialog: false, // 添加任务弹框
      editTaskDialog: false, // 修改任务的弹框
      addTask: {
        // 添加任务
      },
      editTaskTable: {
        // 修改任务表格内容
      },
    };
  },
  methods: {
    // 添加任务
    submitNewTask(ref) {
      this.$refs[ref].validate(valid => {
        if (valid) {
          let {survey,startTime,endTime,isMust,times,tagging} = this.addTask;
          let questionName = survey.split('#')[0]
          let questionId = survey.split('#')[1]
          const params = {
            questionnairePlanItemId : this.questionnairePlanItemId,
            planDtoList:[{
              questionName,questionId,startTime,endTime,isMust,times,tagging
            }]
          }
          this.handleCreateTask(params);
        } else {
          return false;
        }
      });
    },
    // 提交修改任务的表单
    submitTaskTable(ref) {
      this.$refs[ref].validate(valid => {
        if (valid) {
          // console.log(this.editTaskTable)
          let {survey,startTime,endTime,isMust,times,tagging,id} = this.editTaskTable;
          let questionName = survey.split('#')[0]
          let questionId = survey.split('#')[1]
          const params = {
            id,questionName,questionId,startTime,endTime,isMust,times,tagging
          }
         this.handleUpdateTask(params);
        } else {
          return false;
        }
      });
    },
    // 医生信息修改
    edit(row) {
      let resourece = _.cloneDeep(row);
      let {questionId,questionName} = resourece;
      resourece.survey = `${questionName}#${questionId}`
      this.editTaskTable = resourece;
        
      this.editTaskDialog = true;
    },
    // 创建任务
    async handleCreateTask(params){
      let res = await createTask(params);
      let {code,message} = res.data;
      if(code == 200){
        this.addTaskDialog = false;
        this.addTask = {};
        this.handleGetList({itemId : this.questionnairePlanItemId});
      } else {
        this.$message.error(message);
      }
    },
    // 修改任务
    async handleUpdateTask(query){
      let res = await updateTask(query);
      let {code,message} = res.data;
      if(code == 200){
        this.editTaskDialog = false;
        this.handleGetList({itemId : this.questionnairePlanItemId});
      } else {
        this.$message.error(message);
      }
    },
    // 获取任务列表
    async handleGetList(params) {
      // this.loading = true;
      let res = await getTaskList(params);
      // this.loading = false;
      let {
        code,
        data,
        message
      } = res.data;
      if (code === 200) {
        this.tableData = data;
      } else {
        this.$message.error(message);
      }
    },
    // 获取所有问卷
    async handleGetAll(params){
      let res = await getAllSurvey(params);
      let {code,data,message} = res.data;
      if(code == 200){
        let {surveys} = data;
        this.allSurvey = surveys

      } else {
        this.$message.error(message);
      }
    }

  },
  filters:{
    parseIsMust(status){
      return status == 1 ? '是' : '否'
    },
    parseTimes(status){
      return status == 1 ? '1次' : '不限'
    }
  },
  created() {
    let {questionnairePlanItemId , currentDay,duration,useStatus} = this.$route.query;
    this.useStatus = useStatus;
    this.questionnairePlanItemId = questionnairePlanItemId;
    this.currentDay = currentDay;
    this.duration = parseInt(duration)
    this.handleGetList({itemId : questionnairePlanItemId});
    this.handleGetAll({
          "limit":10,
          "currentPage":1
        })
    
  },
};
</script>
