<template>
  <!-- 头部导航 header -->
  <div>
    <a-layout-header class="header">
      <div class="logoContent">MP2多中心项目管理平台</div>
      <div class="logout">
        <a-dropdown :trigger="['click']" :overlayStyle="{top:'5px'}">
          <div class="triggerBtn">
              <img :src="role == 'root'? headImg1 : headImg2" class="headImg">
              <span>{{name}}</span>
              <a-icon type="down" /> 
          </div>
          <a-menu slot="overlay"  @click="onClick">
            <a-menu-item key="logout">
              退出登录
            </a-menu-item>
          </a-menu>
        </a-dropdown>
      </div>
    </a-layout-header>
  </div>
</template>
<script>
import { mapState } from 'vuex';
export default {
  data() {
    return {
      headImg1:require('../assets/head1.png'), // root headimg
      headImg2:require('../assets/head2.png'), // admin headimg
    }
  },
  methods: {
    onClick({key}) {
      if(key == 'logout'){
        this.$store.commit('setLoginInfo',{});
        this.$store.commit('setToken','');
        this.$router.replace('/login')
      }
    }
  },
  computed: {
    ...mapState({
      username : state => state.loginInfo.username,
      role : state => state.loginInfo.role,
      name :state => state.loginInfo.name
    })
  },
};
</script>
